@include jigsass-component(l-wrapper) {
  // 1. Generate clearfix util (`u-cf`).
  // 2. Generate auto horizontal margins util (`u-mh--auto`).
  // 3. Generate 100% width util (`u-w--100%`).
  // 4. Set wrapper widths in different breakpoints.
  // ---
  .l-wrapper {
    @include jigsass-util(u-cf);  // [1]
    @include jigsass-util(u-mh, $modifier: auto); // [2]
    @include jigsass-util(u-w, $modifier: 100%); // [3]

    @each $bp-name, $value in map-get($jigsass-sizes, l-width) {
      @include jigsass-mq($from: $bp-name) {
        $_max-width: jigsass-get-size(l-width, $unit: px);

        @include jigsass-rem(max-width, $_max-width); // [4]
      }
    }
  }
}





