// -------------------------------------
// Base
// -------------------------------------

@include jigsass-component(l-two-cols) {
// stylelint-disable
/* ---
 section: Layout
 title: two columns
 ---
 A main-column-sidebar layout.

```example:html
<main class='l-wrapper u-mh--auto'>
  <div dir="#{$jigsass-direction}" class="l-two-cols  [ o-grid  o-grid--gutter-0 ]">
    <article class= "l-two-cols__main
                [ o-grid__item  o-grid__item--col  o-grid__item--12-of-12 ]">
      <div style="height: 200px; background-color: #{jigsass-color(primary)}"></div>
    </article>

    <aside
      class= "l-two-cols__aside
        [ o-grid__item  o-grid__item--col  o-grid__item--12-of-12 ]">
