// -------------------------------------
// Items
// -------------------------------------

// Main column of a two column layout
//
// Occupies the width of a layout wrapper minus the sidebar.
//
// Extends o-grid__item classes
// ---
// HTML template:
// 	<article class= "l-two-cols__main
// 							[ o-grid__item  o-grid__item--col  o-grid__item--12-of-12 ]">
// 		<!-- Content here -->
// 	</article>
// ---
@include jigsass-component(l-two-cols__main) {
  .l-two-cols__main {
    @each $bp-name, $value in map-get($jigsass-sizes, l-sidebar) {
      @include jigsass-mq($from: $bp-name) {
        $_wrapper-width: jigsass-get-size(l-width, $unit: px);
        $_aside-width: jigsass-get-size(l-sidebar, $unit: px);
        $_width: $_wrapper-width - $_aside-width;

        @include jigsass-rem(flex-basis, $_width);
        @include jigsass-rem(max-width, $_width);

        .no-flexbox & {
          @include jigsass-rem(width, $_width);
        }
      }
    }
  }
}


// Aside column of a two column layout
//
// Extends o-grid__item classes
// ---
// HTML template:
// 	<aside
// 		class= "l-two-cols__aside
// 			     [ o-grid__item  o-grid__item--col  o-grid__item--12-of-12 ]">
// 		<!-- Content here -->
// 	</aside>
// ---
@include jigsass-component(l-two-cols__aside) {
  .l-two-cols__aside {
    @each $bp-name, $value in map-get($jigsass-sizes, l-sidebar) {
      @include jigsass-mq($from: $bp-name) {
        $_width: jigsass-get-size(l-sidebar, $unit: px);

        @include jigsass-rem(flex-basis, $_width);
        @include jigsass-rem(max-width, $_width);

        .no-flexbox & {
          @include jigsass-rem(width, $_width);
        }
      }
    }
  }
}

// Aside column's content wrapper.
// Provides a gutter rule.
//
// ---
// HTML template:
// 	<aside class="l-two-cols__aside u-d--f
// 			          [ o-grid__item  o-grid__item--col  o-grid__item--12-of-12 ]">
//
// 	  <div class="l-two-cols__aside__contnet"
// 		  <!-- Content here -->
// 	  </div>
// 	</aside>
// ---
@include jigsass-component(l-two-cols__aside__content) {
  .l-two-cols__aside__content {
    @include jigsass-mq($from: 'l') {
      flex-grow: 1;
      position: relative;

      &:before {
        @include jigsass-bidi(start, jigsass-rhythm(-4));
        @include z-index('l-two-cols__aside-rule');

        background-color: jigsass-color('border-neutral');
        bottom: 0;
        content: '';
        height: 100%;
        position: absolute;
        top: 0;
        width: 1px;
      }

      &--b:before {
        content: none;
      }
    }
  }
}





