// *************************************
//
// # BEZEQ
// -> Styles for the bezeq widjet
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

//





@mixin c-bezeq {
  @include jigsass-component(bezeq) {
    // -------------------------------------
    // Base
    // -------------------------------------
    // .c-bezeq {}





    // -------------------------------------
    // Modifiers
    // -------------------------------------

    .c-bezeq__sec-head {
      @include jigsass-util('u-bgc', $modifier: 'bezeq');
      @include jigsass-util('u-c', $modifier: 'text--inverse');
      @include jigsass-util('u-ovx', $modifier: 'h');
      @include jigsass-util('u-p', $modifier: 1);
      @include jigsass-util('u-tov', $modifier: 'e');
      // @include jigsass-util('u-type', $modifier: 'epsilon');
      @include jigsass-util('u-whs', $modifier: 'nw');
    }

    .c-bezeq__logo {
      @include jigsass-util('u-h', $modifier: 1em);
      @include jigsass-util('u-w', $modifier: 2.5em);
    }

    .c-bezeq__results {
      @include jigsass-util('u-autospace', $modifier: 3);
      @include jigsass-util('u-autospace', $modifier: 4, $until: 's');
      @include jigsass-util('u-bgc', $modifier: 'bezeq-pale');
      @include jigsass-util('u-c', $modifier: 'bezeq');
      @include jigsass-util('u-p', $modifier: 2);
      @include jigsass-util('u-pt', $modifier: 1);
    }

    .c-bezeq__result {
      @include jigsass-util('u-pos', $modifier: 'r');
    }

    .c-bezeq__result-header {
      @include jigsass-util('u-bdb', $modifier: '2px(1,solid)');
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-mb', $modifier: 1);
      @include jigsass-util('u-pl', $modifier: 12);
      @include jigsass-util('u-pl', $modifier: 14, $from: 's');
    }

    .c-bezeq__result-rating {
      @include jigsass-util('u-c', $modifier: 'text-muted');
      @include jigsass-util('u-end', $modifier: 1);
      @include jigsass-util('u-is-visually-hidden');
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-t', $modifier: 1);
      @include jigsass-util('u-t', $modifier: 0, $from: 's');
      @include jigsass-util('u-type', $modifier: 'milli', $until: 's');
    }

    .c-bezeq__result-content {
      @include jigsass-util('u-autospace', $modifier: 1, $until: 's');
      @include jigsass-util('u-cf');
      @include jigsass-util('u-type', $modifier: 'milli');

      // li`s
      @include jigsass-util('u-pe', $modifier: 2);
      @include jigsass-util('u-is-visually-hidden');
      @include jigsass-util('u-td', $modifier: 'u:hover');
      @include jigsass-util('u-td', $modifier: 'u:focus');

    }

    .c-bezeq__result-phone {
      @include jigsass-util('u-c', $modifier: 'text-pale');
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-td', $modifier: 'u');
    }

    .c-bezeq__result-details {
      @include jigsass-util('u-fl', $modifier: 'end', $from: 's');
    }





    // -------------------------------------
    // Items
    // -------------------------------------

    //





    // -------------------------------------
    // States
    // -------------------------------------

    //





    // -------------------------------------
    // Context
    // -------------------------------------

    //





  }
}

