// A map of styles to use for styling items' `:after`
// pseudo element and separate between items.
// ---
// @type Boolean|Map
// ---
$o-crumbs-separator: (
  content: '\0020/\0020',
  display: inline-block,
  margin-left: 1rem,
  margin-right: 1rem,
);
