// *************************************
//
// #OBJECTS-BLOCK-LINK
// -> Blocky links that occupy their
//    container's entire area
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

$_o-block-link-i: 0;

/* ---
section: Block Link
title: CSS Output
order: #{$_o-block-link-i + 1}
---
```scss
$o-block-link-config
```


**Type:** `Map`

Configuration map for enabling generation
of classes and modifier classes.

**Default:**
```scss
$o-block-link-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
);
```
*/
$_o-block-link-i: $_o-block-link-i + 1;


/// Configuration map for enabling generation of
/// icon classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-block-link-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;





// -------------------------------------
// Definitions
// -------------------------------------

//
@include jigsass-object(o-block-link, $o-block-link-config) {
  // A link that covers its container's entire area,
  // making the entire ui element clickable
  //
  // Wrapper element must be relatively positioned.
  // ---
  // 1. Place above container's main content,
  //    but bellow clickable sub elements (`.block-link__sub`)
  // 2. Make sure that the content beneath the
  //    block link is visible while maintaining
  //    clickability.
  // 3. Cover container's entire area.
  // ---
  @include jigsass-classname {
    @include z-index('base');   // [1]

    background: transparent;    // [2]
    bottom: 0;                  // [3]
    left: 0;                    // [3]
    margin: 0;
    opacity: 0;                 // [2]
    position: absolute;         // [1, 3]
    right: 0;                   // [3]
    top: 0;                     // [3]

    @at-root {
      $o-block-link__raised: _jigsass-gen-class-name(
        $name: 'o-block-link__raised',
        $from: jigsass-get-obj-mq($type: from),
        $until: jigsass-get-obj-mq($type: until),
        $misc: jigsass-get-obj-mq($type: misc),
        $modifier: ''
      );
      // Clickable elements inside the same container
      // ---
      // as the block-link.
      // 1. Position above the block-link to allow
      //    independent clickability.
      // 2. Give element positioning context, in order to
      // ---
      .#{$o-block-link__raised} {
        @include z-index('above');  // [1]
        position: relative;         // [2]
      }
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;

      // stylelint-disable
/* ---
section: Block Link
title: base
order: #{$_o-block-link-i + 1}
---
A link that covers its container's entire area,
making the entire ui element clickable

Wrapper element must be relatively positioned.

#### Usage:
```example:html
