  <img src="https://unsplash.it/1920/800?random"
       alt="A random placeholder image" />
  <a href="#!">
  <h1 class="u-type--gamma">A clickable element</h1>
  </a>
  <a href="#!" class="o-block-link" tabindex="-1"></a>
</article>
```
*/

/* ---
section: Block Link
title: Raised elements
order: #{$_o-block-link-i + 1}
---
Clickable elements inside the same container

#### Usage:
```example:html
<article class="u-pos--r">
  <a href="#!">
  <img src="https://unsplash.it/1920/800?random"
       alt="A random placeholder image"
       class="o-block-link__raised"
       title="The title is visible because of the `o-block-link__raised` class"/>
  </a>

  <a href="#!">
  <h1 class="u-type--gamma">A clickable element</h1>
  </a>
  <p><a href="#!" class="o-block-link__raised">A nested link</a></p>
  <a href="#!" class="o-block-link" tabindex="-1"></a>
</article>
```
*/
      // stylelint-enable
      $_o-block-link-i: $_o-block-link-i + 1;
    }
  }
}

