// *************************************
//
// # Normalize Forms
// -> Form elements
//
////
/// @group Normalize - 08 - Forms
////
//
// *************************************

@charset 'UTF-8';

// -------------------------------------
// Dependencies
// -------------------------------------

@if (not mixin-exists(jigsass-define-block)) {
  @error '`jigsass-generic-normalize` has a dependency on `jigsass-tools-selectors`. ' +
    'Please import it.';
}





// -------------------------------------
// Definitions
// -------------------------------------

@include jigsass-define-block(normalize-forms) {
  // 1. Change font properties to `inherit` in all browsers (opinionated).
  // 2. Remove the margin in Firefox and Safari.
  button,
  input,
  select,
  textarea {
    font: inherit; // [1]
    margin: 0; // [2]
  }

  // Restore the font weight unset by the previous rule.
  optgroup {
    // stylelint-disable
    font-weight: bold;
    // stylelint-enalble
  }

  // Show the overflow in IE.
  // 1. Show the overflow in Edge.
  // 2. Show the overflow in Edge, Firefox, and IE.
  button,
  input, // [1]
  select { // [2]
    overflow: visible;
  }

  // Remove the inheritance of text transform in Edge, Firefox, and IE.
  // 1. Remove the inheritance of text transform in Firefox.
  button,
  select { // [1]
    text-transform: none;
  }

  // Change the cursor in all browsers (opinionated).
  button,
  [type='button'],
  [type='reset'],
  [type='submit'] {
    cursor: pointer;
  }

  // Restore the default cursor to disabled elements unset by the previous rule.
  [disabled] {
    cursor: default;
  }

  // 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`
  //    controls in Android 4.
  // 2. Correct the inability to style clickable types in iOS.
  button,
  body [type='button'], // [1]
  [type='reset'],
  [type='submit'] {
    -webkit-appearance: button; // [2]
  }

  // Remove the inner border and padding in Firefox.
  button::-moz-focus-inner,
  input::-moz-focus-inner {
    border: 0;
    padding: 0;
  }

  // Restore the focus styles unset by the previous rule.
  button:-moz-focusring,
  input:-moz-focusring {
    outline: 1px dotted ButtonText;
  }

  // Change the border, margin, and padding in all browsers (opinionated).
  @if ($jigsass-normalize-fieldset) {
    fieldset {
      border: 1px solid #c0c0c0;
      margin: 0 2px;
      padding: 0.35em 0.625em 0.75em;
    }
  }
  @else {
    fieldset {
      border: 0;
      margin: 0;
      padding: 0;
    }
  }

  // 1. Correct the text wrapping in Edge and IE.
  // 2. Correct the color inheritance from `fieldset` elements in IE.
  // 3. Remove the padding so developers are not caught out when they zero out
  //    `fieldset` elements in all browsers.

  legend {
    box-sizing: border-box; // [1]
    color: inherit; // [2]
    display: table; // [1]
    max-width: 100%; // [1]
    padding: 0; // [3]
    white-space: normal; // [1]
  }

  // Remove the default vertical scrollbar in IE.
  textarea {
    overflow: auto;
  }

  // 1. Add the correct box sizing in IE 10-.
  // 2. Remove the padding in IE 10-.
  [type='checkbox'],
  [type='radio'] {
    box-sizing: border-box; // [1]
    padding: 0; // [2]
  }

  // Correct the cursor style of increment and decrement buttons in Chrome.
  [type='number']::-webkit-inner-spin-button,
  [type='number']::-webkit-outer-spin-button {
    height: auto;
  }

  // Correct the odd appearance of search inputs in Chrome and Safari.
  [type='search'] {
    -webkit-appearance: textfield;
  }

  // Remove the inner padding and cancel buttons in Chrome on OS X and
  // Safari on OS X.
  [type='search']::-webkit-search-cancel-button,
  [type='search']::-webkit-search-decoration {
    -webkit-appearance: none;
  }
}
