// -------------------------------------
// Variables
// -------------------------------------

// Used internally for sorting doclets.
$_o-tabpanel-keyframe-generated: false;
$_o-tabs-i: 0;

/* ---
section: Tabs
title: CSS Output
order: #{$_o-tabs-i}
---
#### Tablist containers
```scss
$o-tabs-config
```


**Type:** `Map`

Configuration map for enabling generation
of tablist classes, and modifier classes.

**Default:**
```scss
$o-tabs-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
);
```


#### Individual Tabs
```scss
$o-tab-config
```


**Type:** `Map`

Configuration map for enabling generation
of tab classes, and modifier classes.

**Default:**
```scss
$o-tab-config: (
  no-breakpoint: (
    no-modifier: false,
    tight: false,
  ),
);
```


#### Tab Panels
```scss
$o-tabpanel-config
```


**Type:** `Map`

Configuration map for enabling generation
of tabpanel classes, and modifier classes.

**Default:**
```scss
$o-tabpanel-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
);
```
*/
$_o-tabs-i: $_o-tabs-i + 1;

/// Configuration map for enabling generation
/// of tablist classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-tabs-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;


/// Configuration map for enabling generation
/// of tab classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-tab-config: (
  no-breakpoint: (
    no-modifier: false,
    tight: false,
  ),
) !default;


/// Configuration map for enabling generation
/// of tabpanel classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-tabpanel-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;





// -------------------------------------
// Definitions
// -------------------------------------

@include jigsass-object(o-tabs, $o-tabs-config) {
  @include jigsass-classname {
    @include jigsass-border-bottom(jigsass-rhythm(1, $unit: 'rem'));

    color: jigsass-color('primary');
    font-weight: 700;
    white-space: nowrap;

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: tabs
title: Tab List
order: #{$_o-tabs-i}
---
An element containing tabs

#### Usage:
```example:html
<section id="tabs">
  <h5></h5>
  <ul class="o-inline-list o-tabs" role="tablist">
    <li class="o-tab" role="presentation">
      <a href="#tabpanel1" role="tab" aria-controls="tabpanel1" aria-selected="true"> 1</a>
    </li>
    <li class="o-tab" role="presentation">
      <a href="#tabpanel2" role="tab" aria-controls="tabpanel2"> 2</a>
    </li>
  </ul>

  <section id="tabpanel1" class="o-tabpanel" role="tabpanel" > </section>
