*/
      // stylelint-enable
      $_o-tabs-i: $_o-tabs-i + 1;
    }
  }
}


@include jigsass-object(o-tab, $o-tab-config) {
  @include jigsass-classname {
    > a,
    > button {
      @include jigsass-rem(
        padding,
        jigsass-rhythm(.5, $unit: 'rem') jigsass-rhythm(1, $unit: 'rem')
      );
      display: block;
      transition: all get-anim($duration: 'mid', $easing: 'swift-out');

      @include jigsass-mq($from: 's') {
        @include jigsass-rem(
          padding,
          jigsass-rhythm(.5, $unit: 'rem') jigsass-rhythm(2, $unit: 'rem')
        );
      }


      &:hover,
      &:focus,
      &:active {
        background-color: jigsass-color('bg');
        outline: none;
      }

      &[aria-selected='true'] {
        background-color: jigsass-color('primary');
        color: jigsass-color('text-inverse');

        &:focus {
          background-color: jigsass-color('quinary');
          color: jigsass-color('text');
        }
      }
    }


    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
