@include jigsass-object(o-tabs__panel, $o-tabpanel-config) {
  @include jigsass-classname {
    position: relative;

    &[aria-hidden='true'] {
      display: none;

      &:after {
        opacity: 1;
        visibility: hidden;
      }
    }

    &:after {
      @include jigsass-bidi(start, 0);
      @include z-index('above');

      animation: o-tabpanel-bg forwards get-anim(
        $duration: 'short',
        $easing: 'swift-in',
        $delay: 'brief');
      background-color: jigsass-color('bg-accented');
      content: '';
      height: 100%;
      position: absolute;
      speak: none;
      top: 0;
      visibility: visible;
      width: 100%;
    }

    @at-root (without: all) {
      @if (not $_o-tabpanel-keyframe-generated) {
        @keyframes o-tabpanel-bg {
          99% {
            opacity: 0;
            visibility: visible;
          }

          100% {
            opacity: 0;
            visibility: hidden;
          }
        }

        $_o-tabpanel-keyframe-generated: true !global;
      }
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
