@mixin c-logo {
  @include jigsass-component('c-logo') {
    // A 2-line logo
    .c-logo {
      height: 1em;
      vertical-align: top;
      width: 2.2915em;
    }

    // A 1-line logo
    .c-logo--1-line {
      width: 1369 / 190 * 1em;
    }
  }
}





