/// Animation / transition durations, easings etc.
/// ---
/// @type map
/// ---
/// @prop {Map} duration
///   Normalized animation durations
/// @prop {Number} duration.name
///   A time.
///
/// @prop {Map} easing
/// @prop {String} easing.name
///   A named timing function or a cubic-bezier
/// ---
$animation: (
  duration: (
    xxlong: 2s,
    xlong: 1.5s,
    long: 1s,
    mid: .5s,
    short: .25s,
    brief: .15s,
  ),
  easing: (
    linear: linear,
    swift-in: cubic-bezier(0, 0, .2, 0),
    swift-out: cubic-bezier(.55, 0, .1, 1),
    in: ease-in,
    out: ease-out,
    in-out: ease-in-out,
  ),
);

/* ---
section: Animation
title: get-anim()
---
Use the `get-anim` function to get normalized animation
timing and timing functions:

```scss
get-anim(
  $duration: <duration-name|time>,
  $easing: <timing-function>,
  $delay: <duration-name|time>
)
```

Named durations are:
`long`: #{map-get(map-get($animation, duration), long)}
`mid`: #{map-get(map-get($animation, duration), mid)}
`short`: #{map-get(map-get($animation, duration), short)}
`brief`: #{map-get(map-get($animation, duration), brief)}

Named timing functions are:
`swift-out`: #{map-get(map-get($animation, easing), swift-out)}
`swift-in`: #{map-get(map-get($animation, easing), swift-in)}
`in`: #{map-get(map-get($animation, easing), in)}
`out`: #{map-get(map-get($animation, easing), out)}
`in-out`: #{map-get(map-get($animation, easing), in-out)}
*/


/// Get animation values.
/// ---
/// @param {String | Boolean} $duration
///   Searches for a named time in `$animation.duration`
/// @param {String | Boolean} $easing
///   Searches for a named timing function in `$animation.easing`
/// @param {String | Boolean} $delay
///   Searches for a named time in `$animation.duration`
/// ---
/// @return {Null | List}
/// ---
@function get-anim($duration: false, $easing: false, $delay: false) {
  @if ($duration or $easing or $delay) {
    $value: ();

    $duration: if(
      type-of($duration) == number and index(s ms, unit($duration)),
      $duration,
      map-get(map-get($animation, duration), $duration)
    );
    $delay: if(
      type-of($delay) == number and index(s ms, unit($delay)),
      $delay,
      map-get(map-get($animation, duration), $delay)
    );
    $easing: map-get(map-get($animation, easing), $easing);

    @if ($duration) { $value: append($value, $duration); }
    @if ($delay) { $value: append($value, $delay); }
    @if ($easing) { $value: append($value, $easing); }

    @if (length($value) == 0) {
      @warn 'No animation values were found for: ' +
        if(
          $duration,
          '`$duration`: #{$duration}  ',
          ''
        ) +
        if(
          $delay,
          '`$delay`: #{$delay}  ',
          ''
        ) +
        if(
          $easing,
          '`$easing`: #{$easing}  ',
          ''
        );

      @return null;
    }

    @return $value;
  }
}
