// *************************************
//
// #HOMEPAGE CENTERPIECE
//
// *************************************

@mixin c-centerpiece {
  @include jigsass-component(c-centerpiece) {
    // -------------------------------------
    // Base
    // -------------------------------------
    .c-centerpiece {
      @include jigsass-util('u-autospace', $modifier: 2, $until: 'l');
    }





    // -------------------------------------
    // Modifiers
    // -------------------------------------

    //





    // -------------------------------------
    // Items
    // -------------------------------------

    .c-centerpiece__col {
      // ----- On right column ----- //
      @include jigsass-util('u-autospace', $modifier: 2, $until: 's');
      @include jigsass-util('u-autospace', $modifier: '2(2up)', $from: 's');

      // on grid containing the teasers which are 2up in mobile
      @include jigsass-util('u-autospace', $modifier: 2, $from: 's');

      // ----- On right column ------ //
      @include jigsass-util('u-autospace', $modifier: 2, $from: 'l');
      @include jigsass-util('u-autospace', $modifier: 2, $until: 's');

      // On small items container inside right column
      @include jigsass-util('u-autospace', $modifier: 2, $until: 'l');
    }

    .c-centerpiece__item {
      @include jigsass-util('u-ov', $modifier: 'h');
      @include jigsass-util('u-pos', $modifier: 'r');
      @include jigsass-util('u-type', $modifier: 'milli');

      transition: all get-anim($duration: 'short', $easing: 'swift-out', $delay: 'short');

      &:before {
        @include jigsass-bidi(transform-origin, bottom 'start');

        background-color: rgba(jigsass-color('bg-accented'), 0.7);
        bottom: 0;
        content: '';
        height: 100%;
        position: absolute;
        transform: scale(0, 0);
        transform: scale3d(0, 0, 1);
        transition: all get-anim($duration: 'short', $easing: 'swift-out', $delay: 'short');
        width: 100%;
      }

      &:hover {
        background-color: jigsass-color('bg-accented');

        &,
        &:before {
          transition-duration: get-anim($duration: 'brief');
          transition-timing-function: get-anim($easing: 'swift-in');
        }

        &:before {
          transform: scale(1, 1);
          transform: scale3d(1, 1, 1);
        }
      }
    }

    .c-centerpiece__content {
      @include jigsass-util('u-p', $modifier: 1);
      @include jigsass-util('u-w', $modifier: 100%);

      h3 {
        time {
          @include jigsass-util('u-c', $modifier: 'primaty');
        }

        // On promoted content notice
        @include jigsass-util('u-c', $modifier: 'text-promoted');
        @include jigsass-util('u-type', $modifier: 'milli');
      }
    }

    .c-centerpiece__footer {
      @include jigsass-util('u-b', $modifier: 0);
      @include jigsass-util('u-bdb', $modifier: '1px(1)');
      @include jigsass-util('u-c', $modifier: 'secondary');
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-ph', $modifier: 1);
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-type', $modifier: 'milli');
      @include jigsass-util('u-w', $modifier: 100%);

      // On purchase button
      @include jigsass-util('u-ms', $modifier: 1);
      @include jigsass-util('u-ph', $modifier: 1);
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-type', $modifier: 'micro');
    }


    // ----- Modifiers ----- //
    .c-centerpiece__item--small {
      @include jigsass-util('u-pb', $modifier: 40%, $from: 's');
      @include jigsass-util('u-pb', $modifier: 25%, $from: 'm');
      @include jigsass-util('u-pb', $modifier: 45.4%, $from: 'l');

      .centerpiece__content {
        @include jigsass-util('u-pb', $modifier: 6, $until: 's');
        @include jigsass-util('u-pos', $modifier: 'a', $from: 's');

        figure {
          @include jigsass-util('u-is-hidden', $until: 's');
        }
      }
    }

    .c-centerpiece__item--medium {
      @include jigsass-util('u-pb', $modifier: 14);
      @include jigsass-util('u-pb', $modifier: 40%, $from: 's');
      @include jigsass-util('u-pb', $modifier: 25%, $from: 'm');
      @include jigsass-util('u-pb', $modifier: 45.4%, $from: 'l');

      .centerpiece__content {
        @include jigsass-util('u-pos', $modifier: 'a');
      }
    }

    .c-centerpiece__item--large {
      @include jigsass-util('u-pb', $modifier: 16);
      @include jigsass-util('u-pb', $modifier: 40%, $from: 's');
      @include jigsass-util('u-pb', $modifier: 25%, $from: 'm');
      @include jigsass-util('u-pb', $modifier: 45%, $from: 'l');
      @include jigsass-util('u-type', $modifier: 'epsilon');
      @include jigsass-util('u-type', $modifier: 'gamma', $from: 's');

      .centerpiece__content {
        @include jigsass-util('u-pos', $modifier: 'a');
      }
    }

    .c-centerpiece__item--textual {
      @include jigsass-util('u-type', $modifier: 'beta', $from: 's');
      @include jigsass-util('u-pb', $modifier: 55.72%, $from: 's');
      @include jigsass-util('u-pb', $modifier: 48.8%, $from: 'm');
      @include jigsass-util('u-pb', $modifier: 58.3%, $from: 'l');

      .centerpiece__content {
        @include jigsass-util('u-pb', $modifier: 6, $until: 's');
        @include jigsass-util('u-pos', $modifier: 'a', $from: 's');
      }
    }

    .c-centerpiece__item--textual--b {
      @include jigsass-util('u-pb', $modifier: 237.33%, $from: 's');
      @include jigsass-util('u-pb', $modifier: 204.92%, $from: 'm');
      @include jigsass-util('u-pb', $modifier: 58.3%, $from: 'l');
    }





    // -------------------------------------
    // States
    // -------------------------------------

    //





    // -------------------------------------
    // Context
    // -------------------------------------

    //





  }
}
