// *************************************
//
// #CARD-SETTINGS
//
// *************************************

// -------------------------------------
// Dependencies
// -------------------------------------

@if (
  not (
    global-variable-exists(jigsass-colors) and
    length($jigsass-colors) > 0 and
    function-exists(jigsass-color)
  )
) {
  @error 'Card settings depend on color definitions. Please import ' +
    '`jigsass-tools-color` and `_settings.color.scss` before ' +
    'importing `_settings.card.scss`';
}

@if (not function-exists(get-anim)) {
  @error 'Card settings depend on animation definitions. Please import ' +
    '`_settings.animation` before importing `_settings.card.scss`';
}





// -------------------------------------
// Variables
// -------------------------------------
// The background color of elements containing cards.
//
// Used for setting the background color of
// the `.o-has-card` contextual modifier.
$jigsass-has-card-bgc: jigsass-color('has-card');

// CSS declarations to apply to cards.
//
// Since cards are such a prevalent UI pattern, yet with so
// many variations, subtle or otherwise, JigSass Cards come with defaults that
// are configurable to the last value and declaration. Don't like our defaults?
// Make your own.
// ---
// @type Map
// ---
$jigsass-card-defaults: (
  background-color: jigsass-color('card-bg'),
);

// Card modifiers
//
// CSS declarations for card modifiers, with each modifier being a nested
// map at the first level.
// ---
// @type Map
// ---
$jigsass-card-modifiers: (
  raised: (
    box-shadow: 0 4px 5px -3px rgba(#333, .5),
    position: relative,
    transform-origin: bottom,
    transition: transform get-anim(brief, swift-out),

    '&:after': (
      bottom: 0,
      box-shadow: 0 6px 6px -5px rgba(#333, .3),
      content: '',
      left: 0,
      opacity: 0,
      position: absolute,
      right: 0,
      top: 0,
      transition: opacity get-anim(brief, swift-out),
      z-index: -1,
    ),

    '&:focus, &:hover, &:active': (
      transform: translateY(-1px),

      '&:after': (
        opacity: 1,
      ),
    ),
  ),
  'has-hidden-btn': (
    overflow: hidden,
    position: relative,

    '> .o-card__body': (
      background-color: jigsass-color('card-bg'),
      position: relative,
      transform: translateY(0),
      transition: all get-anim($duration: short, $easing: swift-out),
      z-index: z-index(above),
    ),

    '> .o-card__hidden-btn': (
      bottom: jigsass-rhythm(-5),
      position: absolute,
      transform: translateY(0),
      transition: all get-anim($duration: short, $easing: swift-out),
      width: 100%,
      z-index: z-index(above),
    ),

    '&:focus, &:hover, &:active': (
      '> .o-card__body, > .o-card__hidden-btn': (
        transform: translateY(jigsass-rhythm(-5)),
      ),
    ),
  ),
  'underlined': (
    '> .o-card__footer:after': (
      background-color: currentColor,
      bottom: 0,
      content: '',
      height: 1px,
      position: absolute,
      start: 0,
      transform: scale3d(0, 1, 1),
      transform-origin: 50% 0%,
      transition: all get-anim($duration: 'brief', $easing: 'swift-out', $delay: 'short'),
      width: 100%,
    ),

    '&:hover > .o-card__footer:after': (
      transform: scale3d(1, 1, 1),
      transition-timing-function: get-anim($easing: 'swift-in'),
    ),
  ),
);

// Border style of a card section
//
// A list in the format of `length` `style` `color`
// ---
// @type List
// ---
$jigsass-card-section-border: 1px solid jigsass-color(border-neutral);
