// *************************************
//
// # Normalize HTML5
// -> HTML 5 display definitions
//
////
/// @group Normalize - 03 - HTML5
////
//
// *************************************

@charset 'UTF-8';

// -------------------------------------
// Dependencies
// -------------------------------------

@if (not mixin-exists(jigsass-define-block)) {
  @error '`jigsass-generic-normalize` has a dependency on `jigsass-tools-selectors`. ' +
    'Please import it.';
}





// -------------------------------------
// Definitions
// -------------------------------------

@include jigsass-define-block(normalize-html5) {
  // Add the correct display in IE 9-.
  // 1. Add the correct display in Edge, IE, and Firefox.
  // 2. Add the correct display in IE.
  article,
  aside,
  details, // [1]
  figcaption,
  figure,
  footer,
  header,
  main, // [2]
  menu,
  nav,
  section,
  summary { // [1]
    display: block;
  }

  // Add the correct display in IE 9-.
  audio,
  canvas,
  progress,
  video {
    display: inline-block;
  }

  // Add the correct display in iOS 4-7.
  audio:not([controls]) {
    display: none;
    height: 0;
  }

  // Add the correct vertical alignment in Chrome, Firefox, and Opera.
  progress {
    vertical-align: baseline;
  }

  // Add the correct display in IE 10-.
  // 1. Add the correct display in IE.
  template, // [1]
  [hidden] {
    display: none;
  }
}
