/// Configuration map for enabling generation of
/// icon classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-input-config: (
  no-breakpoint: (
    no-modifier: false,
    raised: false,

    // Size modifiers
    large: false,

    // State modifiers
    is-error: false,
    is-disabled: false,

    // Non-text form widgets
    select: false,
    radio: false,
    checkbox: false,
  ),
) !default;


/// Configuration map for enabling generation of
/// form input classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-input-item-config: (
  note: (
    no-breakpoint: (
      no-modifier: true,
    ),
  ),
  label: (
    no-breakpoint: (
      no-modifier: true,
    ),
  ),
) !default;



