/// Color settings for radio button inputs.
/// ---
/// @type Map
/// ---
$o-input--radio-color: (
  base: jigsass-color('border-neutral'),
  focused: jigsass-color('border-accented' (jigsass-shade: 4)),
  selected: jigsass-color('primary'),
) !default;



/// Color settings for checkbox button inputs.
/// ---
/// @type Map
/// ---
$o-input--checkbox-color: (
  base: jigsass-color('border-neutral-b'),
  focused: jigsass-color('primary'),
  selected: jigsass-color('primary'),
) !default;





