@include jigsass-object('o-input', $o-input-config) {
  // Basic form input styling.
  // ---
  @include jigsass-classname {
    appearance: none;
    background-color: jigsass-color('bg-form');
    border: 0;
    border-radius: 0;
    color: jigsass-color('primary');
    transition: all get-anim($duration: 'short', $easing: 'swift-out');
    vertical-align: middle;
    width: 100%;

    @include jigsass-rem(padding, jigsass-rhythm(.5, $unit: 'px') jigsass-rhythm(1, $unit: 'px'));
    @include jigsass-border-bottom(1px, .5, dashed jigsass-color('quinary'));

    @each $placeholder in
      '::-webkit-input-placeholder' '::-moz-placeholder' ':ms-input-placeholder' '::placeholder'
    {
      &#{$placeholder} {
        color: jigsass-color('primary');
        opacity: 1;
      }
    }

    &:focus {
      background-color: jigsass-color('bg-accented');
      border-bottom-color: jigsass-color('primary');
      border-bottom-style: solid;
      outline: none;
      transtion-timing-function: get-anim($easing: 'swift-in');

      @each $placeholder in
        '::-webkit-input-placeholder' '::-moz-placeholder' ':ms-input-placeholder' '::placeholder'
      {
        &#{$placeholder} {
          color: jigsass-color('text-pale');
        }
      }
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
