  // A styled radio button. Applied to the radio-button element's
  // container (Usually a `label` element)
  // ---
  @include jigsass-classname($modifier: 'radio') {
    @include jigsass-bidi(padding-start, 1.2em);
    cursor: pointer;
    position: relative;

    &,
    & * {
      -webkit-tap-highlight-color: transparent;

      &:focus {
        outline: none;
      }
    }

    [type='radio'] {
      @include jigsass-util('u-is-visually-hidden');

      &:focus {
        & ~ .o-input__label {
          color: jigsass-color('text');
        }
      }
    }

    &__faux-check {
      @include jigsass-bidi(start, 0);

      background-color: transparent;
      border: 2px solid;
      color: map-get($o-input--radio-color, 'base');
      content: '';
      display: block;
      height: 1em;
      pointer-events: none;
      top: 50%;
      touch-events: none;
      transform: translateY(-50%);
      transition: all get-anim($duration: 'brief', $easing: 'swift-out');
      width: 1em;

      &,
      &:before,
      &:after {
        border-radius: 50%;
        content: '';
        position: absolute;
      }

      &:after {
        @include z-index('below');
        background-color: map-get($o-input--radio-color, 'selected');
        display: block;
        height: 100%;
        opacity: 0;
        transform: scale(0);
        transition: all get-anim($duration: 'short', $easing: 'swift-out');
        width: 100%;
      }

      &:before {
        @include z-index('below');
        background-color: jigsass-color('disabled');
        display: block;
        height: 500%;
        left: 50%;
        opacity: 0;
        top: 50%;
        transform: translate(-50%, -50%) scale(0);
        transition: all get-anim($duration: 'short', $easing: 'swift-out');
        width: 500%;
      }

      :focus ~ & {
        border-width: 3px;
        color: map-get($o-input--radio-color, 'focused');

        &:before {
          animation: o-input--radio-in-out get-anim($duration: 'mid') linear;
        }
      }

      :checked ~ & {
        color: map-get($o-input--radio-color, 'selected');

        &:after {
          opacity: 1;
          transform: scale(.6);
        }
      }
    }

    @if (not $_o-input--radio-keyframe-generated) {
