// A form input's label
// ---
@include jigsass-object('o-input__label', map-get($o-input-item-config, label)) {
  @include jigsass-classname {
    color: jigsass-color('text-pale');
    cursor: pointer;

    .o-input--is-error & {
      color: jigsass-color('negative');
    }

    .o-input--is-disabled & {
      color: jigsass-color('text-muted');
      cursor: default;
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Form Inputs
title: Input label
order: #{$_o-input-i + 1}

A form input's annotation.

---
#### Usage:
```example:html
<form>
  <div>
    <label>
      <span class="o-input__label"> </span>
      <input type="text" placeholder=" "  class="o-input" aria-describesby="noteId"/>
    </label>
    <p id="noteId" class="o-input__notes"> </p>
  </div>
</form>

<p>Or:</p>

<form>
  <div>
    <label>
      <span class="o-input__label u-d--b"> </span>
      <input type="text" placeholder=" "  class="o-input" aria-describesby="noteId"/>
    </label>
    <p id="noteId" class="o-input__notes"> </p>
  </div>
</form>
```
*/
      // stylelint-enable
      $_o-input-i: $_o-input-i + 1;
    }
  }
}
