// *************************************
//
// #BUTTON-SETTINGS
//
// *************************************
/* stylelint-disable indentation */

// -------------------------------------
// Dependencies
// -------------------------------------

@if (
  not (
    global-variable-exists(jigsass-colors) and
    length($jigsass-colors) > 0 and
    function-exists(jigsass-color)
  )
) {
  @error 'Button settings depend on color definitions. Please import ' +
    '`jigsass-tools-color` and `_settings.color.scss` before ' +
    'importing `_settings.buttons.scss`';
}
@if (not global-variable-exists(animation)) {
  @error 'Button settings depend on `_settings.animation.scss`. Please import it before ' +
    'importing `_settings.buttons.scss`';
}





// -------------------------------------
// Variables
// -------------------------------------

// Button padding modifiers
//
// A map of button padding modifiers, each key should contain a list of two
// unitless numbers, representing the number of rhythm units used as padding
// on the horizontal and vertical axes, respectively.
// ---
// @prop {List} default
//   Sets the padding of default button object.
// ---
// @type Map
// ---
$jigsass-btn-padding: (
  default: 2 1,
  small: 1 0.5,
  large: 3 3,
);


// The border width and style (and optionally color) of
// outlined buttons
// ---
// @type {List}
$jigsass-btn-outline: 1px solid;


// Style declarations to add to the `.o-btn` class.
// ---
// @type Map
// ---
$jigsass-btn-style: (
  min-width: 0,
  transition: all get-anim(short, swift-out),
);


// Button modifiers
//
// CSS declarations for button modifiers, with each modifier being a nested
// map at the first level.
// ---
// @type Map
// ---
$jigsass-btn-modifiers: (
  primary: (
    background-color: jigsass-color('primary'),
    color: jigsass-color('text-inverse'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      background-color: jigsass-color('quinary' (jigsass-shade: 4) (saturate: 8)),
      // background-color: jigsass-color('primary' (jigsass-shade: 4) (saturate: 4)),
      color: jigsass-color('black'),
      outline: none,
    )
  ),
  primary-text: (
    color: jigsass-color('text-pale'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      color: jigsass-color('primary'),
    )
  ),
  quinary: (
    background-color: jigsass-color('quinary'),
    color: jigsass-color('black'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      background-color: jigsass-color('quinary' (jigsass-shade: 4) (saturate: 8)),
      outline: none,
    )
  ),
  inverse: (
    background-color: jigsass-color('bg'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      color: jigsass-color('primary'),
    ),
  ),
  inverse-b: (
    color: jigsass-color('text-inverse'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      background-color: jigsass-color('bg'),
      color: jigsass-color('primary'),
      outline: none,
    ),
  ),
  neutral: (
    background-color: jigsass-color('neutral' (jigsass-tint: 2)),
    color: jigsass-color('white'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      background-color: jigsass-color('neutral'),
      outline: none,
    )
  ),
  sales: (
    background-color: jigsass-color('sales'),
    color: #fff,

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      background-color: jigsass-color('sales' (jigsass-shade: 6) (saturate: 4)),
      outline: none,
    )
  ),
  facebook: (
    color: jigsass-color('facebook'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      color: jigsass-color('facebook' (jigsass-shade: 6) (saturate: 8)),
    )
  ),
  twitter: (
    color: jigsass-color('twitter'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      color: jigsass-color('twitter' (jigsass-shade: 6) (saturate: 8)),
    )
  ),
  whatsapp: (
    color: jigsass-color('whatsapp'),
    fill: jigsass-color('text-inverse'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      color: jigsass-color('whatsapp' (jigsass-shade: 6) (saturate: 8)),
    )
  ),
  gplus: (
    color: jigsass-color('gplus'),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      color: jigsass-color('gplus' (jigsass-shade: 6) (saturate: 8)),
    )
  ),
  full: (
    display: block,
    max-width: 100%,
    width: 100%,
  ),
  action: (
    border-bottom-color: currentColor,
    font-weight: 700,
    padding-left: 0,
    padding-right: 0,
    position: relative,

    '&:after': (
      background-color: currentColor,
      bottom: -1 * nth($jigsass-btn-outline, 1),
      content: '',
      height: 2px,
      left: 0,
      right: 0,
      position: absolute,
      transform: scaleX(0),
      transition: all get-anim(short, swift-out),
    ),

    '&:hover:enabled, &:focus:enabled, &:active:enabled': (
      outline: none,

      '&:after': (
        transform: scaleX(1),
      ),
    ),
  ),
  action-is-active: (
    '&:after': (
      transform: scaleX(1),
    ),
  ),
  busy: (
    '&, &:hover, &:active, &:focus': (
      background-color: jigsass-color('bg'),
      border-color: jigsass-color('primary'),
      border-radius: 10em,
      color: jigsass-color('text-inverse'),
      display: block,
      min-width: 75%,
      max-width: 75%,
      opacity: .999, // Fix border overflow bug
      outline: none,
      overflow: hidden,
      position: relative,
      transition: all get-anim('mid', 'linear'),
    ),

    '&:after': (
      animation: busy get-anim('xlong', 'linear') infinite alternate,
      background-color: jigsass-color('primary'),
      border-radius: 10em,
      content: '',
      height: 100%,
      position: absolute,
      start: 0,
      top: 0,
      transform: translateX(120%),
      width: 100%,
      will-change: transform,
      z-index: z-index('below'),
    ),
  ),

  click-area-6: (
   height: jigsass-rhythm(6),
   line-height: jigsass-rhythm(6),
   min-width: jigsass-rhythm(6),
   text-align: center,
  ),
  click-area-7: (
   height: jigsass-rhythm(7),
   line-height: jigsass-rhythm(7),
   min-width: jigsass-rhythm(7),
   text-align: center,
  ),
);

// Keyframe animation for busy buttons
@keyframes busy {
  0% {
    opacity: 0;
    transform: translateX(110%);
  }

  20% {
    transform: translateX(110%);
  }

  50% { opacity: 1; }

  80% { transform: translateX(0); }

  100% { transform: translateX(0); }
}

// Style declarations for the `.o-btn-is-disabled` class.
// ---
// @type Map
// ---
$jigsass-btn-disabled: (
  cursor: default,
  opacity: .4,
);
