// -------------------------------------
// Base
// -------------------------------------

@mixin c-article-entry {
  @include jigsass-component(c-article-entry) {
    .c-article-entry {
      @include jigsass-util('u-mt', $modifier: 2);
      @include jigsass-util('u-p', $modifier: 1);
      @include jigsass-util('u-p', $modifier: 2, $from: 's');
      @include jigsass-util('u-autospace', $modifier: 3);

      // text subheads
      h3 {
        @include jigsass-util('u-bdb', $modifier: '1px(1,solid,border-accented)');
        @include jigsass-util('u-c', $modifier: 'primary');
        @include jigsass-util('u-fw', $modifier: 700);
        @include jigsass-util('u-mt', $modifier: 4);
        @include jigsass-util('u-type', $modifier: 'epsilon');
      }

      figure {
        // End to end media
        @include jigsass-util('u-mh', $modifier: min-1);
        @include jigsass-util('u-mh', $modifier: min-2, $from: s);
        @include jigsass-util('u-maw', $modifier: none);

        figcaption {
          @include jigsass-util('u-cf');
          @include jigsass-util('u-fw', $modifier: 700);
          @include jigsass-util('u-mt', $modifier: 1);
          @include jigsass-util('u-mh', $modifier: 1);
          @include jigsass-util('u-type', $modifier: micro);
        }

        // Tags
        @include jigsass-util('u-d', $modifier: 'i');
        @include jigsass-util('u-fw', $modifier: 700);
        @include jigsass-util('u-me', $modifier: 1);
        @include jigsass-util('u-type', $modifier: milli);

        // Text link ad
        @include jigsass-util('u-fw', $modifier: 700);
        @include jigsass-util('u-c', $modifier: 'primary');
      }
    }

    .c-article-entry__related {
      @include jigsass-util('u-bdb', $modifier: '1rem(3,solid,border-accented)');

      h3 {
        @include jigsass-util('u-d', $modifier: 'ib');
        @include jigsass-util('u-mb', $modifier: 2);
      }

      ul {
        @include jigsass-util('u-autospace', $modifier: 1);
        @include jigsass-util('u-c', $modifier: 'primary');
        @include jigsass-util('u-fw', $modifier: 700);
        @include jigsass-util('u-type', $modifier: 'milli');
      }

      // on item location
      @include jigsass-util('u-c', $modifier: 'text-pale');
      @include jigsass-util('u-fw', $modifier: 400);
      @include jigsass-util('u-ps', $modifier: 1);
    }
  }
}





