/* ---
section: Breadcrumbs
title: Configuration
order: 2
---
#### Separator Styles

```scss
$o-crumbs-separator
```


**Type:** `Boolean` or `Map`


A map of styles to use for styling items' `:after`
pseudo element and separate between items.

**Default:** `false`


**Example:**
```scss
$o-crumbs-separator: (
  content: '\0020/\0020',
  display: inline-block,
  margin-left: 1rem,
  margin-right: 1rem,
);
```
*/
/// A map of styles to use for styling items' `:after`
/// pseudo element and separate between items.
/// ---
/// @type Boolean|Map
/// ---
$o-crumbs-separator: false !default;


/* ---
section: Breadcrumbs
title: CSS Output
order: 1
---
```scss
$o-crumbs-config
```


**Type:** `Map`

Configuration map for enabling generation of
breadcrumbs classes and modifier classes.

**Default:**
```scss
$o-crumbs-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
);
```
*/
/// Configuration map for enabling generation of
/// breadcrumbs classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-crumbs-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;




// -------------------------------------
// Definitions
// -------------------------------------

// @example:html
// <ol vocab="https://schema.org/"
//     typeof="BreadcrumbList"
//     class="o-crumbs o-inline-list u-type--micro u-fw--700">
//   <li property="itemListElement" typeof="ListItem" class="o-crumbs__item">
//     <a property="item" typeof="WebPage" href="#!">
//       <span property="name">{{ section1 }}</span>
//     </a>
//     <meta property="position" content="1">
//   </li><li property="itemListElement" typeof="ListItem" class="o-crumbs__item">
//     <a property="item" typeof="WebPage" href="#!">
//       <span property="name">{{ section2 }}</span>
//     </a>
//     <meta property="position" content="2">
//   </li>
// </ol>
@include jigsass-object('o-crumbs', $o-crumbs-config) {
  @include jigsass-classname {
    @include jigsass-util(u-fw, $modifier: 700);

    @if ($o-crumbs-separator) {
      > *:not(:last-child):after {
        @include jigsass-map2styles($o-crumbs-separator);
      }
    }

    > * {
      &:nth-child(odd) {
        color: jigsass-color('primary');
      }

      &:hover > a,
      &:focus > a {
        border-bottom: 2px solid;
      }
    }
    // stylelint-disable
