// *************************************
//
// #OBJECTS-EMBED
// -> Fluid embedded content
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

$_o-embed-i: 0;

/* ---
section: Fluid Embeds
title: CSS Output
order: #{$_o-embed-i}
---
```scss
$o-embed-config
```


**Type:** `Map`

Configuration map for enabling generation of
classes and modifier classes.

**Default:**
```scss
$o-embed-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
);
```
*/
$_o-embed-i: $_o-embed-i + 1;


/// Configuration map for enabling generation of
/// aspect ratio container classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-embed-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;




// -------------------------------------
// Definitions
// -------------------------------------

@include jigsass-object(o-embed, $o-embed-config) {
  // Basic styles for stretching embedded objects to the
  // width of their container
  // ---
  // 1. Hide overflowing elements.
  // 2. Ensure the child element's dimensions can be set.
  // 3. Stretch or limit embedded element to container width.
  // ---
  @include jigsass-classname {
    overflow: hidden; // [1]

    > * {
      display: block; // [2]
      max-width: 100% !important; // [3]
      width: 100% !important; // [3]
    }

    > script {
      display: none;
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Fluid Embeds
title: Embed container
order: #{$_o-embed-i}
---
<strong>.o-embed</strong>

Styles for either stretching embedded content to the width of
its container, if it is too narrow, or limit its width, when too
wide, so that it does not break the layout.


#### Usage:
```html
<div class="o-embed">
  <iframe style="width: 250px; height: 400px; bacground-color: #ccc;" src"..."></iframe>
</div>
```
*/
      // stylelint-enable
      $_o-embed-i: $_o-embed-i + 1;
    }
  }
}
