// *************************************
//
// #MASTHEAD
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

//





@mixin c-masthead {
  @include jigsass-component('c-masthead') {
    // -------------------------------------
    // Base
    // -------------------------------------

    .c-masthead {
      $stop1: jigsass-color('primary');
      $stop2: mix(jigsass-color('primary'), jigsass-color('quinary'), 60%);
      $stop3: jigsass-color('quinary');

      @include jigsass-util('u-pos', $modifier: 'r');

      // Background and accent
      &:before,
      &:after {
        @include z-index('below');
        pointer-events: none;
        position: absolute;
        speak: none;
        touch-events: none;
        width: 100%;
      }

      // Background
      &:before {
        background: linear-gradient(180deg, $stop1, $stop2);
        content: '';
        height: 100%;
        top: 0;

        @include jigsass-mq($from: 's') {
          height: 80%;
        }
      }

      // Accent
      &:after {
        $accent-svg: '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 250 40"><defs><linearGradient id="mastheadAccent" x1="0" x2="0" y1="0" y2="1"><stop stop-color="#{$stop2}" offset="30%" /><stop stop-color="#{$stop3}" offset="100%" /></linearGradient></defs><polygon points="0,20 0,0 256,0" fill="url(#mastheadAccent)" /></svg>'; /* stylelint-disable-line max-line-length */
        content: inline-svg($accent-svg);
        top: 100%;

        @include jigsass-mq($from: 's') {
          top: 80%;
        }
      }
    }





    // -------------------------------------
    // Modifiers
    // -------------------------------------

    //





    // -------------------------------------
    // Items
    // -------------------------------------

    .c-masthead__content-wrapper {
      @include jigsass-util('u-cf');
      @include jigsass-util('u-mh', $modifier: 'auto');
      @include jigsass-util('u-p', $modifier: 1);
      @include jigsass-util('u-p', $modifier: 2, $from: 's');
      @include jigsass-util('u-ph', $modifier: 4, $from: 'l');
      @include jigsass-util('u-pos', $modifier: 'r');
      @include jigsass-util('u-w', $modifier: 100%);

      @include z-index('above');
    }




    .c-masthead__content {
      @include jigsass-util('u-ai', $modifier: 'fe');
      @include jigsass-util('u-d', $modifier: 'f');
      @include jigsass-util('u-pos', $modifier: 'r');
    }




    .c-masthead__logo {
      @include c-logo;
      @include jigsass-util('u-c', $modifier: 'text-inverse');
      @include jigsass-util('u-fxsh', $modifier: 0);

      // Font size is used for resizing the logo's dimensions
      // with one declaration while maintaining its aspect ratio.
      font-size: jigsass-rhythm(10, $unit: 'rem');

      @include jigsass-mq($from: 's') { font-size: jigsass-rhythm(11); }
      @include jigsass-mq($from: 'xl') { font-size: jigsass-rhythm(10); }
    }




    .c-masthead__tools {
      @include jigsass-util('u-end', $modifier: 0);
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-t', $modifier: 0);


      // --- Search -- //
      // On search form
      @include jigsass-util('u-is-hidden', $until: 's');
      @include jigsass-util('u-pe', $modifier: 5);
      @include jigsass-util('u-pos', $modifier: 'r');
      @include jigsass-util('u-va', $modifier: 't');

      // On input label
      @include jigsass-util('u-is-visually-hidden');

      // On search form button
      @include jigsass-util('u-c', $modifier: 'text-pale');
      @include jigsass-util('u-end', $modifier: 0);
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-t', $modifier: 0);
      @include jigsass-util('u-type', $modifier: 'delta');


      // On mobile show-search-modal button
      @include jigsass-util('u-is-hidden', $from: 's');

      // On mobile search modal
      @include jigsass-util('u-p', $modifier: 2);
      @include jigsass-util('u-pt', $modifier: 7);
      @include jigsass-util('u-pos', $modifier: 'f');
      @include jigsass-util('u-w', $modifier: 90%);

      // On search modal form
      @include jigsass-util('u-autospace', $modifier: 2);

      // On search modal input
      @include jigsass-util('u-is-visually-hidden');
      @include jigsass-util('u-type', $modifier: 'gamma');

      // on modal close button
      @include jigsass-util('u-end', $modifier: 0);
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-t', $modifier: 0);

      // On search modal submit button
      @include jigsass-util('u-type', $modifier: 'gamma');
      @include jigsass-util('u-mt', $modifier: 4);



      // --- Accessibility menu -- //
      // On wrapper
      @include jigsass-util('u-pos', $modifier: 'r');

      // On button
      @include jigsass-util('u-type', $modifier: 'delta');

      // On menu
      @include jigsass-util('u-bgc', $modifier: 'bg');
      @include jigsass-util('u-end', $modifier: 0);
      @include jigsass-util('u-mt', $modifier: 1);

      // On menu items
      @include jigsass-util('u-p', $modifier: 1);
      @include jigsass-util('u-ta', $modifier: 'end');
    }




    // 1. Better swipe experience in iOS and IE >= 10.
    .c-masthead__primary-nav {
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-ov', $modifier: 'a');
      @include jigsass-util('u-whs', $modifier: 'nw');

      // On parent nav
      @include jigsass-util('u-ov', $modifier: 'h');

      -webkit-overflow-scrolling: touch; // [1]
      -ms-overflow-style: -ms-autohiding-scrollbar; // [1]

      > :hover {
        color: jigsass-color('text-inverse');
      }

      > * + * {
        @include jigsass-bidi(margin-start, jigsass-rhythm(2));
      }
    }




    .c-masthead__secondary-nav {
      // On parent nav
      @include jigsass-util('u-is-hidden', $until: 'l');
      @include jigsass-util('u-ph', $modifier: 1);
      @include jigsass-util('u-bgc', $modifier: 'bg');

      // On parent ul
      @include jigsass-util('u-type', $modifier: 'micro');
      @include jigsass-util('u-whs', $modifier: 'nw');

      > :hover {
        color: jigsass-color('primary');
      }
    }





    // -------------------------------------
    // States
    // -------------------------------------

    //





    // -------------------------------------
    // Context
    // -------------------------------------

    //





  }
}
