// *************************************
//
// #BASE.LINKS
// -> Basic link styling
//
// *************************************

// Only output styles once and if not in silent mode
@include jigsass-component(base-link) {
  // Reset link styling.
  // 1. Inherit color from parent
  // 2. Don't automatically decorate text
  a {
    color: inherit; // [1]
    cursor: pointer;
    text-decoration: none; // [2]
  }
}
