/// Configuration map for enabling generation of
/// dropdown classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-dropdown-config: (
  no-breakpoint: (
    no-modifier: false,
    up: false,
  ),
) !default;


/// Configuration map for enabling generation of
/// dropdown item classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-dropdown-item-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;



