/// Sets the non structural styles of the `o-dropdown__item` class.
$o-dropdown-item-styles: (
  no-modifier: (),
) !default;





// -------------------------------------
// Definitions
// -------------------------------------

@include jigsass-object('o-dropdown', $o-dropdown-config) {
  // A dropdown menu
  // 1. Remove from the general layout context
  // 2. Prevent lines of text from breaking.
  // 3. Match presentation and semantics.
  // ---
  @include jigsass-classname {
    position: absolute; // [1]
    white-space: nowrap; // [2]

    &[aria-hidden='true'] {
      display: none; // [3]
    }




    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
