#### .o-dropdown
A dropdown element. Must have an `aria-hidden` property by default.

#### Usage:
```example:html
<div id="dropdown" class='u-pos--r'>
  <button class="js-dropdown__toggle [ o-btn o-btn--primary ]">menu</button>

  <ul aria-hidden='true' class="js-dropdown__menu o-dropdown o-list-bare">
    <li><a href="#!" class="o-dropdown__item">Item 1</a></li>
    <li><a href="#!" class="js-dropdown__menu o-dropdown o-list-bare">Item 2</a></li>
    <li><a href="#!" class="js-dropdown__menu o-dropdown o-list-bare">Item 3</a></li>
    <li><a href="#!" class="js-dropdown__menu o-dropdown o-list-bare">Item 4</a></li>
  </ul>
</div>
```
*/
      // stylelint-enable
      $_o-dropdown-i: $_o-dropdown-i + 1;
    }
  }

