/* ---
section: Dialogs
title: Configuration
order: #{$_o-dialog-i + 1}
---

By default the `dialog` object only sets minimal styling, leaving dialog look
and feel as flexible as possible. Additional style can be set though configuration maps.

#### dialog-wrapper
```scss
$o-dialog-wrapper-styles
```

Sets the non structural styles of the `o-dialog-wrapper` class.

**Type**: `Map`

**Default:**
```scss
$o-dialog-wrapper-styles: (
  'background-color': jigsass-color('bg-dialog-overlay'),
);
```

#### dialog
```scss
$o-dialog-styles
```

Sets the non structural styles of the `o-dialog` class.

**Type**: `Map`

**Default:**
```scss
$o-dialog-wrapper-styles: (
  'background-color': jigsass-color('bg'),
);
```

#### Dialog Animation
```scss
$o-dialog-animation
```

Sets the `o-dialog` keyframe animation used in the `o-dialog-wrapper--anim`
and `o-dialog--anim` classes.

**Type**: `Map`

**Default:**
```scss
$o-dialog-animation: (
  0%: (
    display: none,
    opacity: 0,
  ),

  1%: (
    display: block,
    opacity: 0,
    transform: translate(-50%, -50%) scale(0),
  ),

  25%: (
    transform: translate(-50%, -50%) scale(1),
  ),

  100%: (
    opacity: 1,
    transform: translate(-50%, -50%) scale(1),
  ),
);
```


#### Animated Dialog Modifier Styles
```scss
$o-dialog-anim-mods
```

Sets the styling of the `o-dialog-wrapper--anim` and `o-dialog--anim` modifiers.

**Type**: `Map`

**Default:**
```scss
$o-dialog-anim-mods: (
  animation: o-dialog get-anim($duration: 'long', $easing: 'swift-out') forwards,
  transform: translate(-50%, -50%) scale(0),
);
```
*/
$_o-dialog-i: $_o-dialog-i + 1;


/// Sets the non structural styles of the `o-dialog-wrapper` class.
$o-dialog-wrapper-styles: (
  'background-color': jigsass-color('bg-dialog-overlay'),
) !default;

/// Sets the non structural styles of the `o-dialog` class.
$o-dialog-styles: (
  'background-color': jigsass-color('bg'),
) !default;

/// Sets the styling of the `o-dialog-wrapper--anim` and `o-dialog--anim` modifiers.
$o-dialog-anim-mods: (
  'animation': o-dialog get-anim($duration: 'long', $easing: 'swift-out') forwards,
  'transform': translate(-50%, -50%) scale(0),
) !default;

/// Keyframe animation for dialog windows
$o-dialog-animation: (
  0%: (
    'display': none,
    'opacity': 0,
  ),

  1%: (
    'display': block,
    'opacity': 0,
    'transform': translate(-50%, -50%) scale(0),
  ),

  25%: (
    'transform': translate(-50%, -50%) scale(1),
  ),

  100%: (
    'opacity': 1,
    'transform': translate(-50%, -50%) scale(1),
  ),
) !default;





