@include jigsass-object('o-dialog-wrapper', $o-dialog-wrapper-config) {
  // Dialog-wrapper overlay
  // 1. Stretch to container's dimensions
  // 2. Better rendering of translated contet.
  // 3. Match presentation and semantics.
  // ---
  @include jigsass-classname {
    @include z-index('modal');

    bottom: 0;  // [1]
    cursor: pointer;
    height: 100%; // [1]
    left: 0;  // [1]
    position: absolute; // [1]
    right: 0; // [1]
    top: 0; // [1]
    transform-style: preserve-3d; // [2]
    width: 100%; // [1]

    &[aria-hidden='true'] {
      display: none; // [3]
    }


    @include jigsass-map2styles($o-dialog-wrapper-styles);

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
