title: Wrapper
order: #{$_o-dialog-i + 1}
---
#### .o-dialog-wrapper
A wrapper for dialog element(s). Used as an overlay to hide the content
concealed by the dialog. Extend with a `u-pos--f` class to turn a dialog
into a modal.

#### Usage:
```html
<div class='u-pos--r'>
  <div id="conceal">
    <!-- CONTENT -->
  </div>

  <div id="dialog"
      class='o-dialog-wrapper'
      data-htz-dialog-elem-to-hide='conceal'
      data-htz-dialog-hide
      tabindex='-1'
      aria-hidden='true'>

    <!-- DIALOG CONTENT -->

  </div>
```
*/
      // stylelint-enable
      $_o-dialog-i: $_o-dialog-i + 1;
    }
  }

  // A dialog-wrapper in which dialog windows are transitioned in.
  @include jigsass-classname($modifier: 'anim') {
    @at-root {
      $wrapper-selector: _jigsass-gen-class-name(
        $name: 'o-dialog-wrapper',
        $from: jigsass-get-obj-mq($type: from),
        $until: jigsass-get-obj-mq($type: until),
        $misc: jigsass-get-obj-mq($type: misc),
        $modifier: $jigsass-obj-modifier
      );
      $dialog-selector: _jigsass-gen-class-name(
        $name: 'o-dialog',
        $from: jigsass-get-obj-mq($type: from),
        $until: jigsass-get-obj-mq($type: until),
        $misc: jigsass-get-obj-mq($type: misc),
        $modifier: $jigsass-obj-modifier
      );

      .#{$wrapper-selector} > .o-dialog,
      .#{$dialog-selector} {
        @include jigsass-map2styles($o-dialog-anim-mods);
      }
    }

    @at-root (without: all) {
      @if (length($o-dialog-animation) > 0 and not $_o-dialog-keyframe-generated) {
        @keyframes o-dialog {
          @include jigsass-map2styles($o-dialog-animation);
        }

        $_o-dialog-keyframe-generated: true !global;
      }
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
