@include jigsass-object('o-dialog', $o-dialog-config) {
  // A dialog window
  // 1. Position at center of positioning context.
  // 4. Prevent dialog from exceeding the height of its parent
  // 3. Match presentation and semantics.
  @include jigsass-classname {
    @include jigsass-bidi(start, 50%); // [1]

    cursor: default;
    max-height: 90%; // [2]
    overflow: auto; // [2]
    position: absolute; // [1]
    top: 50%; // [1]
    transform: translate(-50%, -50%); // [1]

    &:focus {
      outline: none;
    }

    &[aria-hidden='true'],
    [aria-hidden='true'] > & {
      display: none; // [3]
    }

    @include jigsass-map2styles($o-dialog-styles);

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
