// *************************************
//
// #BASE.MEDIA
// -> Basic embedded media styling
//
// *************************************

// Only output styles once and if not in silent mode
@include jigsass-component(base-media) {
  // Fluid embedded media
  // 1. Set apart alt text for images that failed to load
  // 2. Fluid embedded media
  // 3. Lose unwanted whitespace beneath embedded media
  // ---
  img,
  video {
    font-style: italic; // [1]
    max-width: 100%; // [2]
    vertical-align: middle; // [3]
  }

  // Respect dimensions explicitly set in the markup
  // ---
  [width],
  [height] {
    max-width: none;
  }

  // Some images should be allowed to scale up
  // ---
  // stylelint-disable string-quotes
  [data-srcset],
  [srcset],
  [width="100%"],
  picture > * {
    // stylelint-enable string-quotes
    width: 100%;
  }
}
