// *************************************
//
// #DROPDOWN-OBJECT-SETTINGS
// -> Settings for dropdown and modal windows
//
// *************************************
/* stylelint-disable indentation */

// -------------------------------------
// Variables
// -------------------------------------

/// Sets the non structural styles of the `o-dropdown__item` class.
$o-dropdown-item-styles: (
  no-modifier: (
    background-color: jigsass-color('bg'),
     box-shadow: 0 4px 5px -2px rgba(#333, .5),
    color: jigsass-color('primary'),
    transition: all get-anim('short', 'swift-out'),

    '&:focus, &:hover': (
      background-color: jigsass-color('primary'),
      color: jigsass-color('bg'),
    ),
  ),
);
