// *************************************
//
// #HERO SEARCH
// -> A large search component
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

//





// -------------------------------------
@mixin c-hero-search {
  // Base
  // -------------------------------------

  @include jigsass-component(c-hero-search) {
    .c-hero-search {
      @include jigsass-util('u-bgc', $modifier: 'bg:before', $until: 's');
      @include jigsass-util('u-mb', $modifier: 8);
      @include jigsass-util('u-pos', $modifier: 'r');
      @include jigsass-util('u-pb', $modifier: 2, $from: 'm');
      @include jigsass-util('u-pt', $modifier: 6, $from: 's');
      @include jigsass-util('u-type', $modifier: 'epsilon');
      @include jigsass-util('u-type', $modifier: 'delta', $from: 'l');

      // Background and accent in small bps
      @include jigsass-mq($until: 's') {
        $bg-color: jigsass-color('bg');

        &:before,
        &:after {
          @include z-index('hero-search-bg');

          content: '';
          position: absolute;
          speak: none;
          width: 100%;
        }

        &:before {
          background-color: $bg-color;
          height: 100%;
          top: 0;
        }

        &:after {
          $accent-svg: '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 250 20"><polygon points="256,10 0,0 256,0" fill="#{$bg-color}" /></svg>'; /* stylelint-disable-line max-line-length */

          content: inline-svg($accent-svg);
          top: 100%;
        }
      }
    }





    // -------------------------------------
    // Modifiers
    // -------------------------------------

    //





    // -------------------------------------
    // Items
    // -------------------------------------

    .c-hero-search__inputs {
      @include jigsass-util('u-pv', $modifier: 6);
      @include jigsass-util('u-pv', $modifier: 1, $from: 's');
      @include jigsass-util('u-w', $modifier: 100%);

      &:after {
        content: '';
        opacity: 0;
      }

      &.is-sticky {
        $stop1: mix(jigsass-color('primary'), jigsass-color('quinary'), 60%);
        $stop2: mix(jigsass-color('primary'), jigsass-color('quinary'), 20%);
        $stop3: jigsass-color('quinary');

        $accent-svg: '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 250 15"><defs><linearGradient id="heroSearchAccent" x1="0" x2="0" y1="0" y2="1"><stop stop-color="#{$stop2}" offset="30%" /><stop stop-color="#{$stop3}" offset="100%" /></linearGradient></defs><polygon points="256,3 0,0 256,0" fill="#{$stop3}"></polygon></svg>'; /* stylelint-disable-line max-line-length */

        @include z-index(hero-search);

        background: linear-gradient(180deg, $stop1, $stop3);
        transition: background-color get-anim($duration: 'mid', $easing: 'swift-out');

        &:after {
          bottom: 0;
          content: inline-svg($accent-svg);
          opacity: 1;
          pointer-events: none;
          position: absolute;
          touch-events: none;
          transform: translateY(100%);
          transition: opacity get-anim($duration: 'mid', $easing: 'swift-out');
          width: 100%;
        }

        button {
          background-color: jigsass-color('bg');
          color: jigsass-color('primary');

          &:hover,
          &:focus,
          &:active {
            background-color: jigsass-color('primary');
            color: jigsass-color('text-inverse');
          }
        }
      }

      > .l-wrapper {
        @include jigsass-util('u-ph', $modifier: 6);
        @include jigsass-util('u-ph', $modifier: 2, $from: 's');
        @include jigsass-util('u-ph', $modifier: 20, $from: 'l');
      }

      .c-hero-search__input-list {
        @include jigsass-util('u-autospace', $modifier: 2, $until: 's');

        > li:first-child {
          @include jigsass-util('u-mt', $modifier: 2, $until: 's');
        }
      }

      .c-hero-search__title {
        @include jigsass-util('u-is-visually-hidden', $from: 's');
        @include jigsass-util('u-ta', $modifier: 'c');
        @include jigsass-util('u-type', $modifier: 'delta');
      }


      select,
      input {
        @include jigsass-util('u-h', $modifier: 100%);
        @include jigsass-util('u-bgc', $modifier: 'bg', $from: 's');
      }

      select {
        @include jigsass-util('u-is-hidden', $until: 's');
      }

      // On free text input, to provide
      // positioning context for caret
      label {
        @include jigsass-util('u-pos', $modifier: 'r');
      }
    }

    .c-hero-search__caret {
      :placeholder-shown + & {
        @include jigsass-bidi(start, jigsass-rhythm(.7));

        animation: o-hero-search__caret 0.7s linear infinite alternate;
        background-color: jigsass-color('primary' (jigsass-tint: 10));
        height: 100%;
        opacity: 1;
        pointer-events: none;
        position: absolute;
        top: 0;
        touch-events: none;
        width: 1px;
      }

      :focus + & {
        display: none;
      }
    }

    @keyframes o-hero-search__caret {
      0% { opacity: 0; }

      40% { opacity: 0; }

      60% { opacity: 1; }
    }





    // -------------------------------------
    // States
    // -------------------------------------

    //





    // -------------------------------------
    // Context
    // -------------------------------------

    //




  }
}
