// *************************************
//
// #BASE.TYPOGRAPHY
// -> Basic typographic styling
//
// *************************************

// Only output styles once and if not in silent mode
@include jigsass-component(base-typography) {
  // Body text
  //
  // 1. Set good measure.
  // 2. Larger type in tiny viewports
  // ---
  .b-entry > *,
  .b-paragraph {
    margin-left: auto;
    margin-right: auto;
    max-width: 92rem;

    @include jigsass-mq($until: s) {
      @include jigsass-font-size(epsilon, $lines: 5); // [2]
    }
  }

  // Textual links
  //
  // 1. A more elegant underline for link interaction
  // ---
  .b-entry > * > a,
  .b-paragraph > a,
  .b-txt-link {
    &,
    &:visited {
      box-shadow: inset 0 -1px 0 0 jigsass-color('link' (jigsass-tint: 15));  // [1]
      color: jigsass-color('link');
    }

    &:hover,
    &:focus,
    &:active  {
      box-shadow: inset 0 -3px 0 0 jigsass-color('link' (jigsass-tint: 15));  // [1]
    }
  }
}
