// *************************************
//
// #OBJECTS-TAG
//
////
/// @group Tag
////
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------


// Used internally for sorting doclets.
$_o-tag-i: 0;


/* ---
section: Tag
title: CSS Output
order: #{$_o-tag-i}
---
```scss
$o-tag-config
```


**Type:** `Map`

Configuration map for enabling generation
of classes, and modifier classes.

**Default:**
```scss
$o-tag-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
);
```
*/
$_o-tag-i: $_o-tag-i + 1;

/// Configuration map for enabling generation
/// of classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-tag-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;





// -------------------------------------
// Definitions
// -------------------------------------

// A simple tag element
@include jigsass-object(o-tag, $o-tag-config) {
  @include jigsass-classname {
    @include jigsass-bidi(margin-end, jigsass-rhythm(1));
    @include jigsass-rem(padding, 0 jigsass-rhythm(1));
    @include jigsass-rem(margin-bottom, jigsass-rhythm(1));

    background-color: jigsass-color('bg-accented' (jigsass-tint: 6));
    border-radius: 2px;
    transition: background-color get-anim($duration: short, $easing: swift-out);

    &:hover,
    &:focus,
    &:active {
      background-color: jigsass-color('bg-accented');
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Tag
title: Base
order: #{$_o-tag-i}
---
An element in an itemized list of topics

#### Usage:
```example:html
<div class="o-inline-list">
  <h5></h5>
  <ul class="o-inline-list">
    <li class="o-tag"><a href=#!> 1</a></li>
    <li class="o-tag"><a href=#!> 2</a></li>
    <li class="o-tag"><a href=#!> 3</a></li>
    <li class="o-tag"><a href=#!> 4</a></li>
    <li class="o-tag"><a href=#!> 5</a></li>
  </ul>

</div>
```
*/
      // stylelint-enable
      $_o-tag-i: $_o-tag-i + 1;
    }
  }
}
