// *************************************
//
// #TOP AD
// -> Styles for pre-masthead advert.
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

//





@mixin c-top-ad {
  @include jigsass-component('c-top-ad') {
    // -------------------------------------
    // Base
    // -------------------------------------

    .c-top-ad {
      @include jigsass-util('u-is-hidden', $until: 's');
      @include jigsass-util('u-bgc', $modifier: 'masthead');
      @include jigsass-util('u-p', $modifier: 2);
    }





    // -------------------------------------
    // Modifiers
    // -------------------------------------

    //





    // -------------------------------------
    // Items
    // -------------------------------------

    .c-masthead__content-wrapper {
      @include jigsass-util('u-cf');
      @include jigsass-util('u-mh', $modifier: 'auto');
      @include jigsass-util('u-p', $modifier: 1);
      @include jigsass-util('u-p', $modifier: 2, $from: 's');
      @include jigsass-util('u-ph', $modifier: 4, $from: 'l');
      @include jigsass-util('u-w', $modifier: 100%);
    }

    .c-masthead__content {
      @include jigsass-util('u-ai', $modifier: 'fe');
      @include jigsass-util('u-d', $modifier: 'f');
    }

    .c-masthead__logo {
      @include c-logo;
      @include jigsass-util('u-c', $modifier: 'text-inverse');

      // Font size is used for resizing the logo's dimensions
      // with one declaration while maintaining its aspect ratio.
      font-size: 9rem;
      // @include jigsass-mq($from: 's') { font-size: 9rem; }
      @include jigsass-mq($from: 'l') { font-size: 11rem; }
      @include jigsass-mq($from: 'xl') { font-size: 10rem; }
    }

    // 1. Better swipe experience in iOS and IE >= 10.
    .c-masthead__primary-nav {
      @include jigsass-util('u-d', $modifier: 'f');
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-ov', $modifier: 'a');
      @include jigsass-util('u-type', $modifier: 'milli');
      @include jigsass-util('u-whs', $modifier: 'nw');

      // On parent nav
      @include jigsass-util('u-ov', $modifier: 'h');

      -webkit-overflow-scrolling: touch; // [1]
      -ms-overflow-style: -ms-autohiding-scrollbar; // [1]

      > :hover {
        color: jigsass-color('text-inverse');
      }

      > * + * {
        @include jigsass-bidi(margin-start, jigsass-rhythm(2));
      }
    }

    .c-masthead__secondary-nav {
      // On parent nav
      @include jigsass-util('u-is-hidden', $until: 'l');
      @include jigsass-util('u-ph', $modifier: 1);
      @include jigsass-util('u-bgc', $modifier: 'bg');

      // On parent ul
      @include jigsass-util('u-type', $modifier: 'micro');
      @include jigsass-util('u-whs', $modifier: 'nw');

      > :hover {
        color: jigsass-color('primary');
      }
    }

    .c-masthead__bg {
      @include jigsass-util('u-c', $modifier: 'masthead');
      @include jigsass-util('u-h', $modifier: 100%);
      @include jigsass-util('u-h', $modifier: 80%, $from: 's');
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-t', $modifier: 0);
      @include jigsass-util('u-w', $modifier: 100%);

      @include z-index('below');

      > svg {
        @include jigsass-util('u-w', $modifier: 100%);
        @include jigsass-util('u-b', $modifier: 0);

        transform: translateY(100%);
      }
    }




    // -------------------------------------
    // States
    // -------------------------------------

    //





    // -------------------------------------
    // Context
    // -------------------------------------

    //





  }
}

