// *************************************
//
// #TYPOGRAPHY-SETTINGS
// -> Typographic settings
//
// *************************************
/* stylelint-disable indentation */

// -------------------------------------
// Dependencies
// -------------------------------------

@if (not global-variable-exists(jigsass-breakpoints)) {
  @error 'Typographic settings depend on breakpoint settings. \a' +
    'Please import `_settings.mq.scss` before importing `_settings.typography.scss`';
}




// -------------------------------------
// Variables
// -------------------------------------

// The default output unit.
// ---
// @type String
// ---
$jigsass-default-unit: rem;

// Should px fallback values be outputted when outputting rems.
// ---
// @type Boolean
// ---
$jigsass-rem-px-fallback: false;



// User-defined ratios for modular scales.
//
// Extends the default ratios:
//   golden: 1.618,
//   minor-second: 1.067,
//   major-second: 1.125,
//   minor-third: 1.2,
//   major-third: 1.25,
//   perfect-fourth: 1.333,
//   augmented-fourth: 1.414,
//   perfect-fifth: 1.5,
//   minor-sixth: 1.6,
//   major-sixth: 1.667,
//   minor-seventh: 1.778,
//   major-seventh: 1.875,
//   octave: 2,
//   major-tenth: 2.5,
//   major-eleventh: 2.667,
//   major-twelfth: 3,
//   double-octave: 4,
//
// **Define your own values BEFORE importing `jigsass-tools-typography.**
// ---
// @type Map
// ---
// @link http://www.modularscale.com
// ---
$jigsass-ratios: ();

// The default ratio used to construct modular scales.
// ---
// @type String
// ---
$jigsass-default-ratio: major-third;


// Reusable responsive named sizes
//
// A per-breakpoint mapping of name to sizes formatted as
// `name: size` or `name: base multiplier [ratio]`
// ---
// @property {Map} size
//   Values of given size at different breakpoints
// @property {Number | List | String} size.breakpoint
//   The assigned size at the given breakpoint.
//
//   When a `List`: First item is the base for multiplication,
//   second item is the multiplier, third, optional, item is the ratio.
//   If there are only two items in the `name` list, ratio
//   will resolve to the value of `$jigsass-default-ratio`.
//
//   When a `String`: An alias to another size.
// ---
// @type Map
// ---
$jigsass-sizes: (
  // Base
  rhythm-unit: (
    default: 6px,
    xl: 7px,
  ),
  body: (
    default: 16px,
    xl: 18px,
  ),
  min-line-padding: (
    default: 2px,
  ),


  // Small print
  nano: (
    default: body -3 major-second,
    xl: body -3 major-second,
  ),
  micro: (
    default: body -2 major-second,
    xl: body -2 major-second,
  ),
  milli: (
    default: body -1 major-second,
    xl: body -1 major-second,
  ),

  // Headings
  zeta: (
    default: body,
    xl: body,
  ),
  epsilon: (
    default: body 1 major-second,
    xl: body 1 major-second,
  ),
  delta: (
    default: body 1,
    xl: body 1,
  ),
  gamma: (
    default: body 2,
    xl: body 2,
  ),
  beta: (
    default: body 3,
    xl: body 3,
  ),
  alpha: (
    default: body 4,
    xl: body 4,
  ),

  // Hero sizes
  kilo: (
    default: body 5,
    xl: body 5,
  ),
  mega: (
    default: body 6,
    xl: body 6,
  ),
  giga: (
    default: body 7,
    xl: body 7,
  ),
  tera: (
    default: body 8,
    xl: body 8,
  ),

  // Layout Width
  // 1. Deduct scrollbar width
  // ---
  l-width: (
    s: map-get(map-get($jigsass-breakpoints, lengths), s),
    m: map-get(map-get($jigsass-breakpoints, lengths), m),
    l: map-get(map-get($jigsass-breakpoints, lengths), l) - 17px, // [1]
    xl: map-get(map-get($jigsass-breakpoints, lengths), xl) - 17px, // [1]
  ),

  // Sidebar width in two column layouts
  // 1. The width of an ad, plus gutters.
  // ---
  l-sidebar: (
    l: 348px,   // [1]
    xl: 356px,  // [1]
  )
);

// A map holding named lists of font-family stacks.
// ---
// @type Map
// ---
$jigsass-font-stacks: (
  default: ('Open Sans Hebrew' arial, sans-serif),
  alt: (arial, sans-serif),
  advert: (arial, sans-serif),
);

// Set the font family of the html element.
// stylelint-disable font-family-name-quotes
$jigsass-normalize-default-font-family: map-get($jigsass-font-stacks, default);
// stylelint-enable font-family-name-quotes
