// *************************************
//
// #SEARCH RESULTS FILTER
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

//





@mixin c-search-results-filter {
  @include jigsass-component(search-results-filter) {
    // -------------------------------------
    // Base
    // -------------------------------------
    .c-search-results-filter {
      @include jigsass-util('u-autospace', $modifier: 2);
      @include jigsass-util('u-bgc', $modifier: 'bg-neutral-dark');
      @include jigsass-util('u-d', $modifier: 'f');
      @include jigsass-util('u-fxd', $modifier: 'c');
      @include jigsass-util('u-mah', $modifier: 96vh, $from: 'l');
      @include jigsass-util('u-mb', $modifier: 4, $until: 'l');
      @include jigsass-util('u-p', $modifier: 2);
      @include jigsass-util('u-pos', $modifier: 'stck', $from: 'l');
      @include jigsass-util('u-t', $modifier: 2, $from: 'l');
    }





    // -------------------------------------
    // Modifiers
    // -------------------------------------

    //





    // -------------------------------------
    // Items
    // -------------------------------------

    .c-search-results-filter__submit {
      @include jigsass-util('u-type', $modifier: 'delta');
    }


    .c-search-results-filter__header {
      @include jigsass-util('u-fxg', $modifier: 0);
      @include jigsass-util('u-fxs', $modifier: 0);
    }

    .c-search-results-filter__search {
      @include jigsass-util('u-pos', $modifier: 'r');
      @include jigsass-util('u-type', $modifier: 'delta');

      // On accesibility span
      @include jigsass-util('u-is-visually-hidden');

      // On note
      @include jigsass-util('u-c', $modifier: 'text--muted');
      @include jigsass-util('u-type', $modifier: 'micro');
    }

    .c-search-results-filter__main {
      @include jigsass-util('u-autospace', $modifier: 2);
      @include jigsass-util('u-fxg', $modifier: 1);
      @include jigsass-util('u-fxs', $modifier: 0);
      @include jigsass-util('u-ov', $modifier: 'a');
    }

    .c-search-results-filter__section {
      @include jigsass-util('u-bgc', $modifier: 'bg-neutral-c');
      @include jigsass-util('u-type', $modifier: 'milli', $from: 'l');
    }

    .c-search-results-filter__section-head {
      @include jigsass-util('u-bds', $modifier: '.5rem(1,solid,primary)');
      @include jigsass-util('u-bgc', $modifier: 'neutral(jigsass-tint,5)');
      @include jigsass-util('u-d', $modifier: 'f');
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-type', $modifier: 'epsilon');

      // On span
      @include jigsass-util('u-c', $modifier: 'text--inverse');
      @include jigsass-util('u-fxg', $modifier: 1);
      @include jigsass-util('u-pv', $modifier: 1);
      @include jigsass-util('u-ps', $modifier: 1);
    }

    .c-search-results-filter__section-content {
      @include jigsass-util('u-autospace', $modifier: 2);
      @include jigsass-util('u-bds', $modifier: '.5rem(2,solid,quinary)');
      @include jigsass-util('u-p', $modifier: 1);
      @include jigsass-util('u-p', $modifier: 2, $from: 's');
    }

    .c-search-results-filter__time-info {
      @include jigsass-util('u-c', $modifier: 'text-pale');
      @include jigsass-util('u-type', $modifier: 'micro');
    }

    .c-search-results-filter__time-opts {
      @include jigsass-util('u-autospace', $modifier: '1(2up)');

      // on labels
      @include jigsass-util('u-d', $modifier: 'b');
    }

    .c-search-results-filter__clear-or {
      @include jigsass-util('u-c', $modifier: 'text-pale');
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-ta', $modifier: 'c');
    }

    .c-search-results-filter__datepickers {
      @include jigsass-util('u-autospace', $modifier: 1);
    }

    .c-search-results-filter__datepicker {
      @include jigsass-util('u-pos', $modifier: 'r');

      // On label
      @include jigsass-util('u-type', $modifier: 'milli');

      // On input
      @include jigsass-util('u-bgc', $modifier: 'bg');
      @include jigsass-util('u-ph', $modifier: 6);
      @include jigsass-util('u-ta', $modifier: 'start');
      @include jigsass-util('u-is-visually-hidden');

      // On datepicker button
      @include jigsass-util('u-b', $modifier: 1px);
      @include jigsass-util('u-c', $modifier: 'primary');
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-start', $modifier: 0);
      @include jigsass-util('u-type', $modifier: 'delta');

      // On icon
      @include jigsass-util('u-type', $modifier: 'epsilon');
      @include jigsass-util('u-va', $modifier: 'bl');
    }

    .c-search-results-filter__clear-date {
      @include jigsass-util('u-b', $modifier: 1px);
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-end', $modifier: 0);
      @include jigsass-util('u-type', $modifier: 'delta');

      display: block;

      :placeholder-shown ~ & {
        display: none;
      }

      // On icon
      @include jigsass-util('u-type', $modifier: 'zeta');
      @include jigsass-util('u-va', $modifier: 'bl');
    }

    .c-search-results-filter__footer {
      @include jigsass-util('u-mt', $modifier: 4);
      @include jigsass-util('u-fxg', $modifier: 0);
      @include jigsass-util('u-fxs', $modifier: 0);
    }





    // -------------------------------------
    // States
    // -------------------------------------

    //





    // -------------------------------------
    // Context
    // -------------------------------------

    //





  }
}

