/// Configuration map for enabling generation of
/// icon classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-icn-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;





// -------------------------------------
// Definitions
// -------------------------------------

//
@include jigsass-object(o-icn, $o-icn-config) {
  // Basic inline icon
  // 1. Sizing through font-size.
  // 2. Better alignment of icons alongside text.
  // ---
  @include jigsass-classname {
    height: 1em; // [1]
    vertical-align: -0.15em;
    width: 1em; // [1]

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;

      // Prepare html snippet for legend.
      $files: listFiles('images/svg/src');
      // stylelint-disable
      $html: '<style>html{direction:ltr !important}ul{margin:0 -2rem}li{background-color: #fff;color: #444;display: inline-block;margin: 2rem 2rem 0;padding: 1rem 1rem 0;text-align: center;}svg {height: 7rem;width: 7rem;}.demo-icon-name {background-color: #ccc;font-family: monospace;font-weight: 700;margin: 1rem -1rem 0;padding: 0 1rem;}.demo-logo--2lines{width:16rem;color: #{jigsass-color(primary)}}.demo-logo--1line{width:30rem;color: #{jigsass-color(primary)}</style>
<ul>';
      // stylelint-enable

      @each $path, $file in $files {
        @if (str-slice($file, -4) != html) {
          $file: str-slice($file, 1, -5);
          $icon-snippet: '
  <li title="#{$file}">
    <svg class="demo-#{$file}"><use xlink:href="assets/sprite.svg#{'#' + $file}"></use></svg>
    <h3 class="demo-icon-name">#{$file}</h3>
  </li>';

          $html: $html + $icon-snippet;
        }
      }

      $html: $html + '
</ul>';

      // stylelint-disable
/* ---
section: Icons
title: o-icn
order: #{$_icons-i + 1}
---
#### Usage:
```html
<svg class="o-icn [ modifier classes ]">
  <title>Optional title for accesibility</title>
  <use xlink:href="#icn-name"></use>
</svg>
```

#### Icon Accesibility

There are a few possible solutions for icon accessibility, depending on the
context in which they are used.

First, if we dont want to add any alt text (either because theres already
relevant text alongside the icons (e.g., as is often the case with buttons),
the `aria-hidden="true"` attribute should be added to the icon, in order to
make sure screen readers dont try reading the icon out loud:

```html
<button class="js-facebook-share  [ o-btn o-btn--facebook ]">
  <svg aria-hidden="true" class="o-icn">
    <use xlink:href="#icon-facebook"></use>
  </svg>
  Share on Facebook
</button>
```
When there is a link or a button containing only an icon, use the `aria-label`
attribute, preferably on the interactive element (`<a>` or `<button>`), as some
screen readers will ignore the `aria-label` attribute on non-interactive
elements:

```html
<button
        class="js-facebook-share  [ o-btn o-btn--facebook ]"
        aria-label="Share on Facebook">
  <svg aria-hidden="true" class="o-icn">
    <use xlink:href="#icon-facebook"></use>
  </svg>
```

Another option is using the `<title>` element. Its especially useful outside of
interactive elements (where aria-label might not be read by some screen
readers). For instance, if you show yes/no markers in a table column, you could have:

```html
<td>
  <svg class="o-icn">
    <title>Yes</title>
    <use xlink:href="#tick"></use>
  </svg>
</td>
```
</div>


