// *************************************
//
// # EXTRA FORM
// -> Styles for the Extra from
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

//





@mixin c-extra-form {
  @include jigsass-component(extra-form) {
    // -------------------------------------
    // Base
    // -------------------------------------
    // .c-extra-form {}





    // -------------------------------------
    // Modifiers
    // -------------------------------------

    .c-extra-form__title {
      @include jigsass-util('u-d', $modifier: 'f');
      @include jigsass-util('u-bgc', $modifier: 'neutral(jigsass-tint,2)');
      @include jigsass-util('u-c', $modifier: 'text--inverse');
      @include jigsass-util('u-fxg', $modifier: 1);
      @include jigsass-util('u-p', $modifier: 1);
      @include jigsass-util('u-ps', $modifier: 2, $from: 's');
      @include jigsass-util('u-type', $modifier: 'delta');
    }

    .c-extra-form__tagline {
      @include jigsass-util('u-c', $modifier: 'text-muted');
      @include jigsass-util('u-fw', $modifier: 400);
      @include jigsass-util('u-is-hidden', $until: 'l');
    }

    .c-extra-form__form {
      @include jigsass-util('u-autospace', $modifier: 3);
    }

    .c-extra-form__headline {
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-mb', $modifier: 1);
      @include jigsass-util('u-type', $modifier: 'epsilon');
    }

    .c-extra-form__details {
      @include jigsass-util('u-maw', $modifier: 60, $from: 'm');
    }

    .c-extra-form__detail {
      // On label
      @include jigsass-util('u-d', $modifier: 'f');

      // On input wrappers
      @include jigsass-util('u-d', $modifier: 'b');
      @include jigsass-util('u-fxg', $modifier: 1);

      //On notes
      @include jigsass-util('u-type', $modifier: 'micro');
      @include jigsass-util('u-va', $modifier: 't');
    }

    .c-extra-form__detail-label {
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-miw', $modifier: 10);
      @include jigsass-util('u-pe', $modifier: 1);
    }

    .c-extra-form__phone {
      // On prefix input
      @include jigsass-util('u-ms', $modifier: 1);
      @include jigsass-util('u-w', $modifier: 8);
    }

    .c-extra-form__regulations-modal {
      // On wrapper
      @include jigsass-util('u-pos', $modifier: 'f');

      // On dialog
      @include jigsass-util('u-mah', $modifier: 90vh);
      @include jigsass-util('u-maw', $modifier: 65vh);
      @include jigsass-util('u-p', $modifier: 2);
      @include jigsass-util('u-w', $modifier: 90%);

      // On headline
      @include jigsass-util('u-c', $modifier: 'primary');
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-mb', $modifier: 2);
      @include jigsass-util('u-type', $modifier: 'delta');

      // On `ol`
      @include jigsass-util('u-autospace', $modifier: 3);
      @include jigsass-util('u-ps', $modifier: 2em);

      // `li  ul` and multi paragraph `li`s
      @include jigsass-util('u-autospace', $modifier: 2);

      // On close-button
      @include jigsass-util('u-end', $modifier: 0);
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-t', $modifier: 0);
      @include jigsass-util('u-type', $modifier: 'delta');
    }





    // -------------------------------------
    // Items
    // -------------------------------------

    //





    // -------------------------------------
    // States
    // -------------------------------------

    //





    // -------------------------------------
    // Context
    // -------------------------------------

    //





  }
}

