// *************************************
//
// #COLOR-SETTINGS
// -> Color related settings
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

// Color palette definitions.
// ---
// @prop {List} color
// @prop {Color | String} color.value
//   Can be either an explicit color value or a string
//   referencing another color defined in the palette.
// @prop {List} color.adjustments
//   A list of color functions to apply. Each item can
//   either be a map with `function-name: args` pairs,
//   or a string for color functions that do not take
//   adjustment arguments (e.g., compliment).
//
//   Adjustment functions will be executed in the
//   order they are defined, in the following format:
//   `function-name(color.value, args...)`.
//
//   When a adjustment argument is a unitless number,
//   It will resolve to the corresponding step in
//   `$jigsass-adjustment-steps`.
// ---
// @see {vriable} $jigsass-adjustment-steps
// ---
// @type Map
// ---
$jigsass-colors: (
  // Brand colors
  'black': #000,
  'white': #fff,
  'neutral': #2d2d2d,
  'current-color': currentColor,

  'primary': #00829e,
  'secondary': #ed143d,
  'tertiary': #f9b41a,
  'quaternary': #00a064,
  'quinary': #0cc,


  // Use cases
  'text': 'neutral',
  'text-inverse': 'white',
  'text-pale': 'neutral' (jigsass-tint: 6),
  'text-muted': 'neutral' (jigsass-tint: 13),
  'text-promoted': 'tertiary' (jigsass-shade: 8, saturate: 10),

  'border-neutral': 'neutral' (jigsass-tint: 15),
  'border-neutral-b': 'neutral' (jigsass-tint: 12),
  'border-accented': 'primary' (jigsass-tint: 15),
  'border-inverse': 'white',
  'link': 'primary',
  'sales':  #e63d00,

  'bg': 'white',
  'bg-accented': 'primary' (jigsass-tint: 15),
  'bg-dialog-overlay': rgba(#2d2d2d, .95),
  'bg-form': 'primary' (jigsass-tint: 17),
  'bg-neutral': 'neutral' (jigsass-tint: 19),
  'bg-neutral-b': 'neutral' (jigsass-tint: 15),
  'bg-neutral-c': 'primary' (jigsass-tint: 17) (desaturate: 7),
  'bg-neutral-dark': neutral,
  'has-card': 'primary' (jigsass-tint: 17) (desaturate: 7),
  'card-bg': 'white',

  'event': 'secondary',
  'venue': 'tertiary',
  'article': 'quaternary',

  'bezeq': #2b2e8f,
  'bezeq-pale': #c1d8f2,

  // States
  'disabled': 'neutral' (jigsass-tint: 15),
  'negative': 'secondary',
  'positive':   'quaternary',
  'warning': 'tertiary',

  // Social
  'facebook': #3b5998,
  'twitter': #55acee,
  'gplus': #d34836,
  'whatsapp': #25d366,
);

// Predefined adjustment steps for color functions
// in the format of 'function-name: steps'.
//
// If the map has a `default` key, if will be used as the default
// for color functions which don't have explicitly defined steps
//
// A key associated a `Number` value, for incremental steps,
// or with a list of numbers for explicitly defining steps
// (see example).
// ---
// @prop {Percentage | List} default [5%]
//   The default steps used for color functions with no
//   explicitly defined steps.
// ---
$jigsass-adjustment-steps: (
  default: 5%,
);





// -------------------------------------
// Styleguide
// -------------------------------------

@mixin sg-color-swatches($colors: $jigsass-colors) {
  $brand-swatches: '';
  $use-case-swatches: '';
  $state-swatches: '';
  $social-swatches: '';

  $brand-colors: (
    'black',
    'white',
    'neutral',
    'primary',
    'secondary',
    'tertiary',
    'quaternary',
    'quinary',
  );

  $state-colors: (
    'disabled',
    'negative',
    'positive',
    'warning',
  );

  $social-colors: (
    'facebook',
    'twitter',
    'gplus',
    'whatsapp'
  );

  // Brand colors
  @each $color in $brand-colors {
    $brand-swatches: $brand-swatches + '@color: #{jigsass-color($color)} @name: #{$color}
';
  }

  // States
  @each $color in $state-colors {
    $state-swatches: $state-swatches + '@color: #{jigsass-color($color)} @name: #{$color}
';
  }

  // Social
  @each $color in $social-colors {
    $social-swatches: $social-swatches + '@color: #{jigsass-color($color)} @name: #{$color}
';
  }

  // Use cases
  $_all: join($brand-colors, $state-colors, comma);
  $_all: join($_all, $social-colors, comma);
  @each $name, $value in $colors {
    @if (not index($_all, $name)) {
      $use-case-swatches: $use-case-swatches + '@color: #{jigsass-color($name)} @name: #{$name}
';
    }
  }

  // stylelint-disable
/* ---
section: Color Palette
order: -100
---
Use the following colors for creating consistent UIs
*/
