// *************************************
//
// #BASE.PAGE
// -> Basic page styling
//
// *************************************

// Only output styles once and if not in silent mode
@include jigsass-component(base-page) {
  // 1. Set the default color.
  // 2. Ensure the page fills at least the full
  //    height of the viewport.
  // 3. Force vertical scrollbar, for a consistent
  //    UX between pages that are long enough to
  //    scroll, and ones that aren't.
  //    *** BREAKS `position: sticky` ***
  //
  // 4. Better font rendering on OSX
  html {
    color: jigsass-color(text);
    fill: jigsass-color(text);
    min-height: 100%; // [2]
    // overflow-y: scroll; // [3]
    // stylelint-disable declaration-block-properties-order
    -moz-osx-font-smoothing: grayscale; // [4]
    -webkit-font-smoothing: antialiased;  // [4]
    // stylelint-enable declaration-block-properties-order
  }

  body {
    overflow-x: hidden;
  }


  // Export media-query info as a JSON-like
  // string in the `content` property of `body::after`
  @include jigsass-mq-export-length-bps;
}
