// Used internally for sorting doclets.
$_collapsibles-i: 0;


/* ---
section: Collapsibles
title: CSS Output
order: #{$_collapsibles-i}
---
```scss
$o-collapsibles-config
```


**Type:** `Map`

Configuration map for enabling generation of classes,
modifier classes, state classes and item classes.

**Default:**
```scss
$o-collapsibles-config: (
  o-collapsible-is-collapsed: (
    no-breakpoint: (
      no-modifier: false,
    ),
  ),
  o-collapsible-is-expanded: (
    no-breakpoint: (
      no-modifier: false,
    ),
  ),
  o-collapsible__toggle: (
    no-breakpoint: (
      no-modifier: false,
      b: false,
    ),
  ),
  o-collapsible__content: (
    no-breakpoint: (
      no-modifier: false,
    ),
  ),
);
```
*/
$_collapsibles-i: $_collapsibles-i + 1;

/// Configuration map for enabling generation of classes,
/// modifier classes, state classes and item classes.
/// ---
/// @type Map
/// ---
$o-collapsibles-config: (
  o-collapsible-is-collapsed: (
    no-breakpoint: (
      no-modifier: false,
    ),
  ),
  o-collapsible-is-expanded: (
    no-breakpoint: (
      no-modifier: false,
    ),
  ),
  o-collapsible__toggle: (
    no-breakpoint: (
      no-modifier: false,
      b: false,
    ),
  ),
  o-collapsible__content: (
    no-breakpoint: (
      no-modifier: false,
    ),
  ),
) !default;





// -------------------------------------
// Definitions
// -------------------------------------

// A collapsible element's toggle button
// Extends `o-btn` and often a button color modifier in the markup.
@include jigsass-object(
  o-collapsible__toggle,
  map-get($o-collapsibles-config, o-collapsible__toggle)
) {
  @include jigsass-classname {
    height: jigsass-rhythm(6);
    position: relative;
    width: jigsass-rhythm(6);

    &:after,
    &:before {
      background-color: currentColor;
      content: '';
      font-size: jigsass-rhythm(1);
      left: 50%;
      position: absolute;
      top: 50%;
      transition: height get-anim($duration: brief, $easing: swift-in),
        transform get-anim($duration: brief, $easing: swift-in),
        width get-anim($duration: brief, $easing: swift-in);
    }

    &:before {
      height: jigsass-rhythm(.5);
      transform: translate(-50%, -50%) rotate(0) scaleY(1);
      width: jigsass-rhythm(3);
    }

    &:after {
      height: jigsass-rhythm(3);
      transform: translate(-50%, -50%) rotate(0) scaleY(0);
      width: jigsass-rhythm(.5);
    }


    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Collapsibles
title: Toggle element
order: #{$_collapsibles-i}
---
A collapsible element's toggle button
Extends `o-btn` and often a button color modifier in the markup.

Accessibility text is handled by the `htz-collapsibles` javascript module. Must carry the
`js-collapsible__toggle` class as a jvascript hook.

#### Usage:
```html
<button class="js-collapsible__toggle o-collapsible__toggle
              [ o-btn o-btn--<color-modifier ]"             >
</button>
```
*/
      // stylelint-enable
      $_collapsibles-i: $_collapsibles-i + 1;
    }
  }

  // A text-based toggle element drawing content from data attributes
  @include jigsass-classname($modifier: 'b') {
    &:before {
      content: attr(data-collapsed-content);

      .o-collapsible-is-expanded & {
        content: attr(data-expanded-content);
      }
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Collapsibles
title: Toggle element b
order: #{$_collapsibles-i}
---
A collapsible element's text-based toggle element drawing content from data attributes

Text is derived from the `data-collapsed-contnt` attribute for the collapsed state and the
`data-expanded-content` attribute for the expanded state

Accessibility text is handled by the `htz-collapsibles` javascript module.
Must be a descendant of an element carrying the `js-collapsible__toggle` class as a jvascript hook.

#### Usage:
```html
<button class="js-collapsible__toggle>
  <span aria-hidden="true" data-collapsed-content="expand +" data-expanded-content="collapse -"></span>
</button>
```
*/
      // stylelint-enable
      $_collapsibles-i: $_collapsibles-i + 1;

    }
  }
}


// A collapsible element's content block
// 1. Set up collapsing animation.
// ---
@include jigsass-object(
  o-collapsible__content,
  map-get($o-collapsibles-config, o-collapsible__content)
) {
  @include jigsass-classname {
    max-height: 20000px; // [1]
    overflow: hidden;
    transform-origin: 0 0;  // [1]
    transition: all get-anim($duration: brief, $easing: swift-out); // [1]

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Collapsibles
title: Content element
order: #{$_collapsibles-i}
---
A collapsible element's content block

Must carry the `js-collapsible__content` class as a jvascript hook.

#### Usage:
```html
<div class="js-collapsible__content o-collapsible__content">
<!-- content here -->
</div>
```
*/
      // stylelint-enable
      $_collapsibles-i: $_collapsibles-i + 1;
    }
  }
}


// A collapsible element in its collapsed state. Set on the wrapper.
// Can be limited to specific breakpoints using responsive modifiers.
//
// ---
// 1. Change button state.
// 2. Hide content visibly.
// 3. Hide content from screen readers.
// 4. Disable collapsing if javascript not enabled.
// ---
@include jigsass-object(
  o-collapsible-is-collapsed,
  map-get($o-collapsibles-config, o-collapsible-is-collapsed)
) {
  @include jigsass-classname {
    & > * > .o-collapsible__toggle,
    & > .o-collapsible__toggle {
      &:before,
      &:after {
        transform: translate(-50%, -50%) rotate(-180deg) scaleY(1); // [1]
      }
    }

    & > .o-collapsible__content {
      margin: 0; // [2]
      max-height: 0; // [2]
      padding-bottom: 0; // [2]
      padding-top: 0; // [2]
      transform: scaleY(0); // [2]
      visibility: hidden; // [3]

      // [4]
      .no-js & {
        max-height: 20000px;
        transform: scaleY(1);
        visibility: visible;
      }
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
