*/
      // stylelint-enable
      $_collapsibles-i: $_collapsibles-i + 1;
    }
  }
}


// A collapsible element in its expanded state. Set on the wrapper.
// Can be limited to specific breakpoints using responsive modifiers.
//
// ---
// 1. Change button state.
// 2. Reveal content visibly.
// 3. Reveal content to screen readers.
// ---
@include jigsass-object(
  o-collapsible-is-expanded,
  map-get($o-collapsibles-config, o-collapsible-is-expanded)
) {
  @include jigsass-classname {
    & > .o-collapsible__toggle,
    & > * > .o-collapsible__toggle {
      &:before {
        transform: translate(-50%, -50%) rotate(0) scaleY(1); // [1]
      }

      &:after {
        transform: translate(-50%, -50%) rotate(0) scaleY(0); // [1]
      }
    }

    & > .o-collapsible__content {
      transform: scaleY(1); // [2]
      visibility: visible; // [3]
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
