// *************************************
//
// #OBJECTS-SECTION-HEAD
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

$_o-sec-head-i: 0;

/* ---
section: Section Head
title: CSS Output
order: #{$_o-sec-head-i + 1}
---
#### BASE
```scss
$o-sec-head-config
```


**Type:** `Map`

Configuration map for enabling generation
of classes and modifier classes.

**Default:**
```scss
$o-sec-head-config: (
  no-breakpoint: (
    no-modifier: false,
    b: false,
  ),
);
```

#### Items
```scss
$o-sec-head-items-config
```


**Type:** `Map`

Configuration map for enabling generation
of item classes and modifier classes.

**Default:**
```scss
$o-sec-head-items-config: (
  links(
    no-breakpoint: (
      no-modifier: false,
    ),
  ),
);
```
*/
$_o-sec-head-i: $_o-sec-head-i + 1;


/// Configuration map for enabling generation of
/// classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-sec-head-config: (
  no-breakpoint: (
    no-modifier: false,
    b: false
  ),
) !default;

/// Configuration map for enabling generation of
/// item classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-sec-head-items-config: (
  links: (
    no-breakpoint: (
      no-modifier: false,
    ),
  ),
) !default;





// -------------------------------------
// Definitions
// -------------------------------------

@include jigsass-object(o-sec-head, $o-sec-head-config) {
  @include jigsass-classname {
    @include jigsass-util('u-d', $modifier: 'ib'); // <- On heading
    @include jigsass-border-bottom(1px, 1, solid jigsass-color('border-accented'));

    // date
    @include jigsass-util('u-c', $modifier: 'text-pale');
    @include jigsass-util('u-type', $modifier: 'epsilon');

    // dateicker
    @include jigsass-util('u-c', $modifier: 'primary');
    @include jigsass-util('u-d', $modifier: 'ib');
    @include jigsass-util('u-ph', $modifier: 1);

    align-items: baseline;
    color: jigsass-color('primary');
    display: flex;
    justify-content: space-between;

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;

      // stylelint-disable
/* ---
section: Section Head
title: base
order: #{$_o-sec-head-i + 1}
---
A section head.

#### Usage:
```example:html
<h2 class="o-sec-head u-type--epsilon">
    
</h2>
```
*/
      // stylelint-enable
      $_o-sec-head-i: $_o-sec-head-i + 1;
    }
  }

  @include jigsass-classname($modifier: 'b') {
    @include jigsass-border-bottom(1px, 1, solid jigsass-color('primary'));

    color: jigsass-color('text');

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;

      // stylelint-disable
/* ---
section: Section Head
title: base
order: #{$_o-sec-head-i + 1}
---
A darker section head

#### Usage:
```example:html
<h2 class="o-sec-head o-sec-head--b u-type--epsilon">
    
</h2>
```
*/
      // stylelint-enable
      $_o-sec-head-i: $_o-sec-head-i + 1;
    }
  }
}


// Links related to a section head.
@include jigsass-object(o-sec-head__links, map-get($o-sec-head-items-config, links)) {
  @include jigsass-classname {
    @include jigsass-util('u-ms', $modifier: 1);
    @include jigsass-util('u-type', $modifier: 'milli');

    color: jigsass-color('text-pale');
    display: inline-block;

    a:hover,
    a:focus,
    a:active  {
      box-shadow: inset 0 -3px 0 0 jigsass-color('link' (jigsass-tint: 15));  // [1]
      color: jigsass-color('link');
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;

      // stylelint-disable
/* ---
section: Section Head
title: Related links
order: #{$_o-sec-head-i + 1}
---

Links related to a section head.

Require the container to be relatively positioned.

#### Usage:
```example:html
<h2 class="o-sec-head u-type--epsilon u-pos--r">
    
  <ul class="[ o-inline-list o-inline-list--delimited ] o-sec-head__links  u-type--micro">
    <li><a href="#!"> </a></li>
    <li><a href="#!"> </a></li>
  <ul>
</h2>
```
*/
      // stylelint-enable
      $_o-sec-head-i: $_o-sec-head-i + 1;
    }
  }
}
