// *************************************
//
// #OBJECTS-KICKER
// -> Styles for headline kickers
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

$_o-kicker-i: 0;

/* ---
section: Kicker
title: CSS Output
order: #{$_o-kicker-i + 1}
---
```scss
$o-kicker-config
```


**Type:** `Map`

Configuration map for enabling generation
of classes and modifier classes.

**Default:**
```scss
$o-kicker-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
);
```
*/
$_o-kicker-i: $_o-kicker-i + 1;


/// Configuration map for enabling generation of
/// icon classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-kicker-config: (
  no-breakpoint: (
    no-modifier: false,
  ),
) !default;





// -------------------------------------
// Definitions
// -------------------------------------

//
@include jigsass-object(o-kicker, $o-kicker-config) {
  // Basic kicker
  // ---
  @include jigsass-classname {
    color: jigsass-color('secondary');
    display: inline;

    &:after {
      @include jigsass-rem(padding, 0 jigsass-rhythm(0.5, $unit: 'px'));

      color: jigsass-color('text-muted');
      content: '/';
      display: inline-block;
      font-weight: 400;
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;

      // stylelint-disable
/* ---
section: Kicker
title: Inline Kicker
order: #{$_o-kicker-i + 1}
---
A kicker element to accompany healines.

#### Usage:
```example:html
<h1 class="u-type--beta">
  <span class="o-kicker"> </span>   
</h1>
```
*/
      // stylelint-enable
      $_o-kicker-i: $_o-kicker-i + 1;
    }
  }
}
