// *************************************
//
// #OBJECTS-ADVERT
// -> Styling for advertisements and
//    their containers
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

// Used internally for sorting doclets.
$_o-advert-i: 0;

/* ---
section: Adverts
title: CSS Output
order: #{$_o-advert-i}
---
#### Base and general modifiers
```scss
$o-ad-config
```

**Type:** `Map`

Configuration map for enabling generation
of classes and stylistic modifier classes

**Default:**
```scss
$o-ad-config: (
  no-breakpoint: (
    no-modifier: false,
    has-label: false,
    has-bg: false,
  ),
);
```


#### Sizing modifiers

```scss
$o-ad-sizes-config
```

**Type:** `Map`

Configuration map for enabling generation
of ad size modifier classes.

**Default:**
```scss
$o-ad-sizes-config: (
  no-breakpoint: (
    '1280-200': false,
    '980-190': false,
    '970-90': false,
    '970-250': false,
    '728-90': false,
    '468-60': false,
    '320-50': false,
    '300-600': false,
    '300-250': false,
    '160-600': false,
    '120-240': false,
  ),
);
```
*/
$_o-advert-i: $_o-advert-i + 1;


/// Configuration map for enabling generation
/// of classes and stylistic modifier classes.
/// ---
/// @type Map
/// ---
$o-ad-config: (
  no-breakpoint: (
    no-modifier: false,
    has-label: false,
    has-bg: false,
  ),
) !default;

/// Configuration map for enabling generation
/// of ad size modifier classes.
/// ---
/// @type Map
/// ---
$o-ad-sizes-config: (
  no-breakpoint: (
    '1280-200': false,
    '980-190': false,
    '970-90': false,
    '970-250': false,
    '728-90': false,
    '468-60': false,
    '320-50': false,
    '300-600': false,
    '300-250': false,
    '160-600': false,
    '120-240': false,
  ),
) !default;




// -------------------------------------
// Definitions
// -------------------------------------
@include jigsass-object(o-ad, jigsass-deep-merge($o-ad-sizes-config, $o-ad-config)) {
  // Basic ad container
  // ---
  // 1. Normalize adverts' box model and center content.
  // ---
  @include jigsass-classname {
    div,
    iframe {
      display: block; // [1]
      margin: 0 auto; // [1]
      padding: 0; // [1]
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Adverts
title: Ad wrapper
order: #{$_o-advert-i}
---
An ad wrapper that normalizes adverts' box model an centers them.

#### Usage:
```html
<div class="o-ad">
  <iframe src='my-advert'><iframe/>
</div>
```
*/
      // stylelint-enable
      $_o-advert-i: $_o-advert-i + 1;
    }
  }


  // ----- Modifiers ----- //
  // An advert width a `` label above of it
  @include jigsass-classname($modifier: has-label) {
    &:before {
      @include jigsass-font-size(milli);
      @include jigsass-font-size(micro, $bps: xl);

      color: jigsass-color('text-pale');
      content: '\2014\00a0  \2014';
      display: block;
      text-align: center;
    }

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Adverts
title: Labeled Ad
order: #{$_o-advert-i}
---
An advert width a `` label above of it

#### Usage:
```example:html
<div class="o-ad o-ad--has-label">
  <div style="width: 300px; height: 250px; backgound-color: #ccc;"></div>
</div>
```
*/
      // stylelint-enable
      $_o-advert-i: $_o-advert-i + 1;
    }
  }

  // An advert that is contained within a visible box
  @include jigsass-classname($modifier: has-bg) {
    background-color: jigsass-color('bg-neutral');

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Adverts
title: Has Background
order: #{$_o-advert-i}
---
An advert that is contained within a visible box

#### Usage:
```example:html
<div class="o-ad o-ad--has-bg">
  <div style="width: 300px; height: 250px; background-color: #f04;"></div>
</div>
```
*/
      // stylelint-enable
      $_o-advert-i: $_o-advert-i + 1;
    }
  }


  // ----- Sizing modifiers ----- //
  $active-sizes: ();

  @each $bp-name, $values in $o-ad-sizes-config {
    $bp: map-get($o-ad-sizes-config, $bp-name);
    $sizes: map-keys($bp);

    @each $size in $sizes {
      @if (map-get($bp, $size) and not index($active-sizes, $size)) {
        $active-sizes: append($active-sizes, $size);
      }
    }
  }

  @each $size in $active-sizes {
    $dimensions: jigsass-str-split($size, '-');

    @include jigsass-classname($modifier: $size) {
      div,
      iframe {
        height: unquote(nth($dimensions, 2)) + px;
        width: unquote(nth($dimensions, 1)) + px;
      }

      $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
      @if (not index($_doclet-store, $_doclet)) {
        $_doclet-store: append($_doclet-store, $_doclet) !global;
        // stylelint-disable
  /* ---
  section: Adverts
  title: $size
  order: #{$_o-advert-i}
  ---
  A size modifier that sets the advert's wrapper to a width of #{nth($dimensions, 2)} and
  a height of #{nth($dimensions, 1)} in order to prevent reflows.

  #### Usage:
  ```example:html
  <div class="o-ad o-ad--#{$size}">
    <div style="background-color: #f04;">
      <iframe src='my-advert'><iframe/>
    </div>
  </div>
  ```
  */
        // stylelint-enable
        $_o-advert-i: $_o-advert-i + 1;
      }
    }
  }
}
