// *************************************
//
// #LISTING SOCIAL
// -> Collapsible social panel
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------


// Used internally for sorting doclets.
$_listing-social-i: 0;





// -------------------------------------
// Definitions
// -------------------------------------

@mixin c-listing-social {
  @include jigsass-component(c-listing-social) {
    // Collapsible share panels for listings.
    // ---
    // 1. Set up animating on min-width
    // ---
    .c-listing-social {
      @include jigsass-bidi(end, 0);
      @include jigsass-rem(max-width, jigsass-rhythm(7, $unit: px));

      background-color: transparent;
      display: flex;
      height: 100%;
      min-width: 0; // [1]
      position: absolute;
      top: 0;
      transition: all get-anim($duration: short, $easing: swift-out);

      @include jigsass-mq($from: xl) {
        @include jigsass-rem(max-width, jigsass-rhythm(6, $unit: px));
      }
    }

    // ----- Items ----- //
    .c-listing-social__content {
      align-items: center;
      color: jigsass-color(text-inverse);
      display: none;
      fill: jigsass-color('primary');
      flex-grow: 1;
      justify-content: center;

      > * {
        :hover,
        :focus,
        :active {
          color: jigsass-color(primary (jigsass-tint: 16));
        }

        & + * {
          @include jigsass-bidi(margin-start, jigsass-rhythm(1));
        }
      }
    }

    .c-listing-social__toggle {
      @include jigsass-util('u-is-hidden', $from: l);

      background-color: jigsass-color('bg-neutral-b');
      color: jigsass-color('primary');
      height: 100%;
      outline: none;
      transition: all get-anim($duration: mid, $easing: swift-out);

      &:hover,
      &:focus,
      &:active {
        background-color: jigsass-color('bg-accented');
        color: jigsass-color('primary' (jigsass-shade: 6));
      }
    }

    .c-listing-social__close {
      display: none;
    }

    // ----- State ----- //
    .c-listing-social-is-expanded {
      background-color: jigsass-color('primary');
      max-width: 100%;
      min-width: 100%;

      .c-listing-social__toggle {
        background-color: jigsass-color('primary' (jigsass-tint: 2));
        color: jigsass-color('text-inverse');

        &:hover,
        &:focus,
        &:active {
          background-color: jigsass-color('primary' (jigsass-tint: 6));
        }
      }

      .c-listing-social__content {
        display: flex;
      }

      .c-listing-social__open {
        display: none;
      }

      .c-listing-social__close {
        display: block;
        margin: 0 auto;
      }
    }
  }
}
