// *************************************
//
// #OBJECTS-ASPECT-RATIO
// -> Intrinsic ratio containers.
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

$_aspect-ratio-i: 0;

/* ---
section: Aspect Ratio Containers
title: CSS Output
order: #{$_aspect-ratio-i}
---
```scss
$o-aspect-config
```


**Type:** `Map`

Configuration map for enabling generation of
aspect ratio container classes and modifier classes.

**Default:**
```scss
$o-aspect-config: (
  no-breakpoint: (
    no-modifier: false,
    headline: false,
    regular: false,
    landscape: false,
    square: false,
    vertical: false,
  ),
);
```
*/
$_aspect-ratio-i: $_aspect-ratio-i + 1;



/* ---
section: Aspect Ratio Containers
title: Config
order: #{$_aspect-ratio-i}
---
```scss
$o-aspect-ratios
```


**Type:** `Map`

Named aspect ratios

A map of named aspect ratios and their values

**Default:**
```scss
$o-aspect-config: (
  no-breakpoint: (
    // base
    no-modifier: false,

    // ratios
    headline: false,
    regular: false,
    landscape: false,
    square: false,
    vertical: false,

    // context
    has-embed: false,
  ),
);
```
*/
$_aspect-ratio-i: $_aspect-ratio-i + 1;

/// Configuration map for enabling generation of
/// aspect ratio container classes and modifier classes.
/// ---
/// @type Map
/// ---
$o-aspect-config: (
  no-breakpoint: (
    // base
    no-modifier: false,

    // ratios
    headline: false,
    regular: false,
    landscape: false,
    square: false,
    vertical: false,

    // context
    has-embed: false,
  ),
) !default;


/// Named aspect ratios
///
/// A map of named aspect ratios and their values
/// ---
/// @type Map
/// ---
$o-aspect-ratios: (
  headline: 16 / 9,
  regular: 4 / 3,
  landscape: 2.3,
  square: 1 / 1,
  vertical:  0.85,
) !default;




// -------------------------------------
// Definitions
// -------------------------------------

@include jigsass-object(o-aspect, $o-aspect-config) {
  // Basic styles for setting intrinsic ratio containers
  // Should be extended in the html with ratio modifiers.
  // ---
  // 1. Allows us to set intrinsic ratio using `padding-bottom`.
  // 2. Hide overflowing elements.
  // 3. Provide positioning context for embedded elements.
  // ---
  @include jigsass-classname {
    height: 0; // [1]
    overflow: hidden; // [2]
    position: relative; // [3]

    $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
    @if (not index($_doclet-store, $_doclet)) {
      $_doclet-store: append($_doclet-store, $_doclet) !global;
      // stylelint-disable
/* ---
section: Aspect Ratio Containers
title: Base
order: #{$_aspect-ratio-i}
---
Basic styles for setting intrinsic ratio containers.
To set an aspect ratio on an element, the `o-aspect`
class Should be extended with ratio modifiers in the html.


#### Usage:
```html
<div class="o-aspect <modifier>">
</div>
```
*/
      // stylelint-enable
      $_aspect-ratio-i: $_aspect-ratio-i + 1;
    }
  }

  // Create aspect ratio modifiers
  @each $name, $ratio in $o-aspect-ratios {
    @include jigsass-classname($modifier: $name) {
      $ratio: map-get($o-aspect-ratios, $jigsass-obj-modifier);

      padding-bottom: (1 / $ratio) * 100%;

      $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
      @if (not index($_doclet-store, $_doclet)) {
        $_doclet-store: append($_doclet-store, $_doclet) !global;
        // stylelint-disable
/* ---
section: Aspect Ratio Containers
title: #{$jigsass-obj-modifier} ratio modifier
order: #{$_aspect-ratio-i}
---
Set the intrinsic ratio of a container to a fluid height that
is #{(1 / $ratio) * 100%} of its width.

#### Usage:
```example:html
<div class="o-aspect o-aspect--#{$jigsass-obj-modifier}"
     style="background-color:#{jigsass-color(primary)};">
  <h3 style="color: #fff; left: 50%; position: absolute; top: 50%; transform: translate(-50%, -50%)">
  An intrinsic ratio container with '<code>#{$jigsass-obj-modifier}</code>' aspect ratio.
  </h3>
</div>
```
*/
        // stylelint-enable
        $_aspect-ratio-i: $_aspect-ratio-i + 1;
      }
    }
  }
  @include jigsass-classname($modifier: has-embed) {
    // Stretch child elements to size of container.
    // All style carry `!important` so that they can override inline styling
    // ---
    // 1. Ensure the child element's dimensions can be set.
    // 2. Stretch child element to container dimensions.
    // 3. Remove all spacing.
    // ---
    > * {
      display: block !important; // [1]
      height: 100% !important; // [2]
      left: 0 !important; // [2]
      margin: 0 !important; // [3]
      padding: 0 !important; // [3]
      position: absolute !important; // [2]
      top: 0 !important; // [2]
      width: 100% !important; // [2]

      $_doclet: $jigsass-obj-name + ($jigsass-obj-modifier or '');
      @if (not index($_doclet-store, $_doclet)) {
        $_doclet-store: append($_doclet-store, $_doclet) !global;
        // stylelint-disable
/* ---
section: Aspect Ratio Containers
title: Intrinsic ratio embeds
order: #{$_aspect-ratio-i}
---
Stretch child elements to the dimensions of of the intrinsic ratio container.

#### Usage:
```example:html
<div class="o-aspect o-aspect--#{$jigsass-obj-modifier}" style="padding-bottom: 56.25%">
  <iframe width="420" height="315" src="https://www.youtube.com/embed/3fRIUnKJk1w" frameborder="0" allowfullscreen></iframe>
</div>
```
*/
        // stylelint-enable
        $_aspect-ratio-i: $_aspect-ratio-i + 1;
      }
    }
  }
}
