// *************************************
//
// Z-INDEX-SETTINGS
// -> Stacking context settings
//
////
/// @group z-index
////
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

/// An hierarchical representation of the stacking order
/// ---
/// @type map
/// ---
/// @example scss
///   below:          -1,
///   above:           1,
///
///   context-a:      10,
///
///   context-b:     100,
///     context-b__el1:   -1,
///     context-b__el2:   10,
///
///   context-c:    1000,
/// )
/// ---
$z-stack: (
  modal:                    1000,
  hero-search:              900,
  above:                    1,
  base:                     0,
  below:                    -1,
  hero-search-bg:           -2,
  l-two-cols__aside-rule:   -3,
);





// -------------------------------------
// Functions
// -------------------------------------

/// Get a layer's place in the stacking order
/// ---
/// @param {String} $layer
///   The name of the layer to look for in the stacking context.
/// ---
/// @return {Number}
/// ---
@function z-index($layer) {
  $index: map-get($z-stack, $layer);

  @if (not $index) {
    @warn '`#{$layer}` is not a defined stacking layer in `$z-stack`';
    @return null;
  }

  @return $index;
}




// -------------------------------------
// Mixins
// -------------------------------------

/// Get a layer's place in the stacking order
/// ---
/// @param {String} $layer
///   The name of the layer to look for in the stacking context.
/// ---
@mixin z-index($layer) {
  z-index: z-index($layer);
}
