// *************************************
//
// #FOOTER
// -> Site footer style
//
// *************************************

// -------------------------------------
// Variables
// -------------------------------------

//




@mixin c-footer {
  @include jigsass-component('c-footer') {
    // -------------------------------------
    // Base
    // -------------------------------------

    .c-footer {
      @include jigsass-util('u-bgc', $modifier: 'primary');
      @include jigsass-util('u-c', $modifier: 'text-inverse');
      @include jigsass-util('u-pt', $modifier: 4, $from: 's');
      @include jigsass-util('u-pt', $modifier: 6, $from: 'l');
      @include jigsass-util('u-pb', $modifier: 9, $until: 'l');
      @include jigsass-util('u-pos', $modifier: 'r');
    }




    // -------------------------------------
    // Modifiers
    // -------------------------------------

    //





    // -------------------------------------
    // Items
    // -------------------------------------

    .c-footer__slant {
      @include jigsass-util('u-c', $modifier: 'has-card', $until: 's');
      @include jigsass-util('u-pos', $modifier: 'a');
      @include jigsass-util('u-t', $modifier: 0);
      @include jigsass-util('u-w', $modifier: 100%);
    }

    .c-footer__content {
      @include jigsass-util('u-p', $modifier: 4);
    }

    .c-footer__side-col {
      @include jigsass-util('u-autospace', $modifier: 2);
      @include jigsass-util('u-ta', $modifier: 'c', $until: 'l');
    }

    .c-footer__logo {
      // Set size through font-size.
      font-size: 4rem;
      @include jigsass-mq($from: 's') { font-size: 8rem; }
      @include jigsass-mq($from: 'l') { font-size: 10rem; }

      @include jigsass-util('u-d', $modifier: 'b');
      @include jigsass-util('u-start', $modifier: 'min-1.5', $from: 'l');
      @include jigsass-util('u-is-hidden', $from: 'l');
      @include jigsass-util('u-is-hidden', $until: 'l');
      @include jigsass-util('u-pos', $modifier: 'r');
    }

    .c-footer__links {
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-td', $modifier: 'u:hover');
      @include jigsass-util('u-td', $modifier: 'u:focus');
      @include jigsass-util('u-type', $modifier: 'milli');
    }

    .c-footer__social {
      @include jigsass-util('u-autospace', $modifier: 1);
      @include jigsass-util('u-c', $modifier: 'text-pale');
      @include jigsass-util('u-mb', $modifier: 4);
      @include jigsass-util('u-type', $modifier: 'delta');

      // On icon links:
      @include jigsass-util('u-d', $modifier: 'b');

      // On apple and android icons.
      @include jigsass-util('u-is-hidden', $until: 'l');

      // On download app button
      @include jigsass-util('u-fw', $modifier: 700);
      @include jigsass-util('u-mt', $modifier: 4);
      @include jigsass-util('u-is-hidden', $from: 's');
    }

    .c-footer__nav {
      @include jigsass-util('u-is-hidden', $until: 's');
      @include jigsass-util('u-type', $modifier: 'micro');

      > ul {
        @include jigsass-util('u-autospace', $modifier: '4(3up)', $until: 'l');
      }

      h4 {
        @include jigsass-util('u-type', $modifier: 'milli');
        @include jigsass-util('u-mt', $modifier: 4);
      }
    }




    // -------------------------------------
    // States
    // -------------------------------------

    //





    // -------------------------------------
    // Context
    // -------------------------------------

    //





  }
}
